<template>
    <div class="box">
        <div class="header">
            <Row>
                <Col span="8">{{$t('Types')}}</Col>
                <Col span="8">{{$t('Quantity')}}/GABA</Col>
                <Col span="8">{{$t('Time')}}</Col>
            </Row>
        </div>
        <div class="content">
            <Row class="row" v-for="(item, index) of list" :key="index">
                <Col span="8" class="type">{{ item.type == '2' ? $t('ReferralReward') : item.type == '3' ? $t('award') : item.type == '1' ? $t('LuckyPrize') : $t('Receive') }}</Col>
                <Col span="8" v-if="$fromWei(item.amout)>0">{{ '+'+$fromWei(item.amout,2)+'GABA' }}</Col>
				<Col span="8" v-else>{{ $fromWei(item.amout)+'GABA' }}</Col>
                <Col span="8">{{ $toTime(item.time,'Y-M-D') }}</Col>
            </Row>
        </div>
    </div>
</template>

<script>
import { Row, Col } from 'vant'
import axios from 'axios'

export default {
    components: { Row, Col },
    data() {
        return {
            list: [
                // { type: '分红', amount: '+0.5500 ETH', time: '2019/7/16' },
                // { type: '分享奖', amount: '+0.5500 ETH', time: '2019/7/16' },
                // { type: 'M1', amount: '+0.5500 ETH', time: '2019/7/16' },
                // { type: 'M2', amount: '+0.5500 ETH', time: '2019/7/16' },
                // { type: 'M3', amount: '+0.5500 ETH', time: '2019/7/16' },
                // { type: 'M4', amount: '+0.5500 ETH', time: '2019/7/16' },
                // { type: '分红', amount: '+0.5500 ETH', time: '2019/7/16' }
            ]
        }
    },
    methods: {
        async init() {
			var address = this.$address;
			axios.get('https://api-cn.etherscan.com/api',{
				params: {
					"module":'logs',
					"action":'getLogs',
					"fromBlock":9514338,
					"toBlock":'latest',
					"address":this.$contract.mainContract._address,
					"topic0":'0xbd60c87b8021edfe53d2767927920d66fc70d7b6cee571e84ec3a2cd9a80303e',
					"topic1": this.$web3.eth.abi.encodeParameter('address',address),
					"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU',
				}
			}).then( result => {
				result.data.result.forEach( item => {
					var data = this.$web3.eth.abi.decodeParameters(['uint256','int256'],item.data);
					var box = {
						time: data[0],
						amout: data[1],
						address: this.$web3.eth.abi.decodeParameters(['address'],item.topics[1])[0],
						oid: this.$web3.eth.abi.decodeParameters(['uint256'],item.topics[2])[0],
						type: this.$web3.eth.abi.decodeParameters(['uint16'],item.topics[3])[0].substr(0,1),
						typeContent: this.$web3.eth.abi.decodeParameters(['uint256'],item.topics[3])[0].substr(1),
					};
					this.list.unshift(box)
				})
			}).catch( err => {
				console.log(err)
			});
			
			
        }
    },
    mounted() {
        this.init()
    }
}
</script>

<style lang="scss" scoped>
.content {
    width: 100%;
    height: calc(100vh - 184px);
    color: #fff;
    font-size: 20px;
    overflow-y: auto;
    &>div {
       margin-top: 15px;
    }
    .type {
        font-size: 24px;
    }
}
.header {
    width: 100%;
    color: #fff;
    font-size: 24px;
	padding-top: 10px;
}
</style>