<template>
    <div class="box">
        <div class="header">
            <Row>
                <Col span="6">{{$t('PeriodNumber')}}</Col>
                <Col span="6">{{$t('Quantity')}}</Col>
				<Col span="6">{{$t('Status')}}</Col>
                <Col span="6">{{$t('Time')}}</Col>
            </Row>
        </div>
        <div class="content">
            <Row class="row" v-for="(item, index) of list" :key="index">
				<Col span="6">{{ Number(item.index)+1 }}</Col>
                <Col span="6">{{ item.totalAmount }}</Col>
                <Col span="6" v-if='item.statuse == "2"'>{{$t('Crowdfunding')}}</Col>
				<Col span="6" v-if='item.statuse == "3"'>{{$t('Waiting')}}</Col>
				<Col span="6" v-if='item.statuse == "4"'>{{$t('Failure')}}</Col>
				<Col span="6" v-if='item.statuse == "5"'>{{ Number(item.totalStProfit)>0 ? $t('over1') : $t('Waiting') }}</Col>
				<Col span="6" v-if='item.statuse == "6"'>{{ Number(item.totalStProfit)>0 ? $t('EndApportionment') : $t('RiskSharing') }}</Col>
                <Col span="6">{{ item.latestDepositedTime }}</Col>
            </Row>
        </div>
    </div>
</template>

<script>
import { Row, Col } from 'vant'

export default {
    components: { Row, Col },
    data() {
        return {
            list: [
               
            ]
        }
    },
    methods: {
        init() {
            this.$contract.mainContract.methods.DepositedRoundOIDS(this.$address).call({from:this.$address},(err,result)=>{
            	this.list = [];
            	result.ids.forEach(async (item)=>{
            		var list = await this.$contract.mainContract.methods.DepositedInfo(this.$address,item).call();
            		var box = {
            			statuse : list.statuse,
            			latestDepositedTime : this.$toTime(list.latestDepositedTime,'Y-M-D'),
            			totalAmount : this.$fromWei(list.totalAmount),
            			index : item,
						totalStProfit : list.totalStProfit,
            		};
            		this.list.push(box)
            		this.list.sort(function(a,b){
            			return a.index - b.index
            		})
            	})
            })
        }
    },
    mounted() {
        this.init()
    }
}
</script>

<style lang="scss" scoped>
.content {
    width: 100%;
    height: calc(100vh - 184px);
    color: #fff;
    font-size: 20px;
    overflow-y: auto;
    &>div {
       margin-top: 15px;
    }
    .type {
        font-size: 24px;
    }
}
.header {
    width: 100%;
    color: #fff;
    font-size: 24px;
	padding-top: 10px;
}
</style>