<template>
	<popup v-model="showFun" @click-overlay='clickOverlay'>
		<div class="authorizedAccessBox">
			<!-- <div class="authorizedAccessTop">
				<div><img src="../assets/yellowRing.png"/></div>
				<p>{{$t('Participation')}}</p>
			</div> -->
			<div class="authorizedAccessTop">
				<div><img src="../assets/yellowRing.png"/></div>
				<p>{{$t('quotaEmpower')}}</p>
				<span>{{authorizedAccessTime}}</span>
			</div>
			<div class="authorizedAccessText" v-html="$t('quotaEmpowerText1')">合约额度授权指合约对资产可操控的授信额度，<br/>与真实可用额度无关，推荐使用多少授权多少。</div>
			<div class="authorizedAccessInput">
				<p>{{$t('quotaEmpowerText2')}}: {{needAuthorizationFun}} {{tokenTypeFun}}</p>
				<div>
					<input type="number" v-model="authorizedAccessNumber" @input="authorizedAccessInput" placeholder="授权额度" />
					<div>{{tokenTypeFun}}</div>
				</div>
				<span>{{$t('quotaEmpowerText3')}} {{availableAuthorizationsFun}} {{tokenTypeFun}}</span>
			</div>
			<div class="authorizedAccessButton" @click="click">{{$t('Authorizations')}}</div>
		</div>
	</popup>
</template>

<script>
	import { Popup } from 'vant';
	export default {
		components:{ Popup },
		data () {
			return {
				showFun: this.show,
				needAuthorizationFun: this.needAuthorization,
				availableAuthorizationsFun: this.availableAuthorizations,
				authorizedAccessTime: '',
				authorizedAccessNumber: '',
				tokenTypeFun: this.tokenType
			}
		},
		props:{
			show:{
				type: Boolean,
				default: false
			},
			click:{
				type: Function
			},
			needAuthorization: {},
			availableAuthorizations: {},
			tokenType: {
				type: String
			},
			authorizationEnds:{
				type: Boolean,
				default: false
			},
		},
		watch:{
			show(){
				this.showFun = this.show;
				this.authorizedAccessTime = this.$toTime(new Date().getTime()/1000,'Y/M/D H:i')
			},
			needAuthorization(){
				this.needAuthorizationFun = this.needAuthorization
			},
			availableAuthorizations(){
				this.availableAuthorizationsFun = this.availableAuthorizations
			},
			authorizationEnds(){
				if(this.authorizationEnds){
					this.authorizedAccessNumber = '';
				}
			},
			tokenType(){
				this.tokenTypeFun = this.tokenType
			}
		},
		methods:{
			authorizedAccessInput(){
				this.authorizationEnds = false;
				this.$emit('authorizedAccessInput',this.authorizedAccessNumber)
			},
			clickOverlay(){
				this.authorizedAccessNumber = ''
				this.$emit('authorizationEnds','end')
			}
		}
	}
</script>

<style scoped lang="scss">
	.authorizedAccessBox{
		width: 700px;
		height: 906px;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 60px 40px 0;
		background:rgba(23,87,154,.87);
		border-radius:20px;
		
		&>div{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		
		.authorizedAccessTop{
			width: 100%;
			font-size: 32px;
			border-bottom:2px solid rgba(6,33,54,1);
			color: #A1BDE1;
			padding-bottom: 28px;
			margin-bottom: 46px;
			
			&>div{
				width: 106px;
				height: 106px;
				border-radius: 50%;
				// border: 5px solid #FFCE00;
				margin-bottom: 30px;
				
				img{
					width: 106px;
					height: 106px;
				}
			}
			
			&>span{
				font-size: 24px;
			}
		}
		
		.authorizedAccessText{
			position: relative;
			font-size: 28px;
			color: #DEE6EF;
			margin-bottom: 46px;
		}
		
		.authorizedAccessText:after{
			position: absolute;
			content: '';
			width: 6px;
			height: 32px;
			background-color: rgba(58,156,255,1);
			left: -15px;
			top: 3px;
		}
		
		.authorizedAccessInput{
			width: 100%;
			font-size: 26px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: 56px;
			
			&>div{
				display: flex;
				font-size: 36px;
				width: 100%;
				height: 86px;
				border: 2px solid #3A9CFF;
				border-radius: 10px;
				align-items: center;
				margin: 18px 0 14px;
				
				&>div{
					display: flex;
					align-items: center;
					justify-content: center;
					width: 194px;
					height: 100%;
					font-size: 40px;
					border-left: 2px solid #3A9CFF;
				}
				
				&>input{
					flex: 1;
					width: 100%;
					height: 100%;
					background-color: transparent;
					border: none;
					padding-left: 38px;
				}
				
			}
			
			&>p{
				color: #DEE6EF;
				text-align: left;
			}
			
			&>span{
				color: #A1BDE1;
			}
		}
		
		.authorizedAccessButton{
			width: 390px;
			height: 80px;
			background: #3F8DE0;
			border-radius: 10px;
			font-size: 32px;
		}
	}
</style>
