<template>
	<div class="box">
		<div class="exchangeRecord">
			<p>{{$t('RevenueRecord')}}</p>
			<div class="exchangRecordContent">
				<div class="exchangRecordList">
					<div><span>{{$t('Types')}}</span></div>
					<div><span>{{$t('income')}}</span></div>
				</div>
				<div class="exchangRecordList" v-for="(item,index) in fundRecord" :key="index">
					<div>
						<span v-if="item.type == '1'">{{$t('ReturnOnEquity')}}</span>
						<span v-else-if="item.type == '2'">{{$t('ReferralReward1')}}</span>
						<span v-else-if="item.type == '3'">{{$t('PrincipalWithdrawal')}}</span>
						<span v-else-if="item.type == '4'">{{$t('Partake')}}</span>
						<span>{{$toTime(item.time,'M.D H:i')}}</span>
					</div>
					<div>
						<span v-if="item.type == '1' || item.type == '2'">+{{item.nxx}} NXX</span>
						<span v-else-if="item.type == '4'">-{{item.onef}} ONEF</span>
						<span v-else-if="item.type == '3'">+{{item.onef}} ONEF</span>
					</div>
				</div>
				<div class="noRecord" v-if="Record">
					<img src="../../../assets/noRecord.png"/>
					<span>{{$t('NoTransaction')}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return {
				Record: false,
				fundRecord: [],
			}
		},
		mounted() {
			axios.get('https://api-cn.etherscan.com/api',{
				params: {
					"module":'logs',
					"action":'getLogs',
					"fromBlock":9514338,
					"toBlock":'latest',
					"address":this.$contract.financialPlan._address,
					"topic0":'0xea8dc96c6d025d56c785bf4e82e35f81014e2dab1f6371b3a8a24f4ae477aaf4',
					"topic1": this.$web3.eth.abi.encodeParameter('address',this.$address),
					// "topic3": this.$web3.eth.abi.encodeParameter('uint16','100'),
					"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU',
				}
			}).then( result => {
				// console.log(result)
				if(result.data.result.length == 0){
					this.Record = true
				}
				result.data.result.forEach( item => {
					var data = this.$web3.eth.abi.decodeParameters(['uint256','uint256','uint256'],item.data);
					this.fundRecord.unshift({
						time: data[0],
						onef: this.$fromWei(data[1]),
						nxx: this.$fromWei(data[2]),
						type: this.$web3.eth.abi.decodeParameters(['uint256'],item.topics[2])[0]
					})
				})
			}).catch( err => {
				console.log(err)
			});
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		width: 100vw;
		height: 100vh;
		padding: 26px 28px 0;
		
		.exchangeRecord{
			width: 100%;
			height: 1082px;
			background-color: rgba($color: #4A7EB5, $alpha: 0.39);
			border-radius: 10px;
			font-size: 28px;
			padding: 0 35px;
			display: flex;
			flex-direction: column;
			position: relative;
			
			&>p{
				padding: 10px 0 8px;
			}
			
			&>.exchangRecordContent{
				flex: 1;
				overflow-y: scroll;
				
				&>.exchangRecordList{
					font-size: 24px;
					margin-bottom: 10px;
					display: flex;
					justify-content: space-between;
					align-items: center;
					
					&>div{
						width: 30%;
						display: flex;
						flex-direction: column;
					}
				}
				
				&>.noRecord{
					font-size: 24px;
					color: #758D9C;
					display: flex;
					flex-direction: column;
					align-items: center;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
					
					&>img{
						width: 67px;
						height: 85px;
					}
				}
			}
		}
		
	}
</style>
