import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/home/Home.vue'
import income from '../views/income/income.vue'
import My from '../views/my/My.vue'
import Exchange from '../views/exchange/exchange.vue'
import Pledge from '../views/pledge/pledge.vue'

import IncomeRecord from '../views/home/view/IncomeRecord.vue'
import JoinRecord from '../views/home/view/JoinRecord.vue'
import Join from '../views/home/view/Join.vue'
import Record from '../views/home/view/Record.vue'
import MyLnvitationList from '../views/my/view/MyLnvitationList.vue'
import About from '../views/my/view/About.vue'
import Change from '../views/exchange/change.vue'
import Fund from '../views/exchange/fund.vue'
import FundRecord from '../views/exchange/view/fundRecord.vue'
import PledgeGuide from '../views/pledge/pledgeGuide.vue'
import PledgeGABA from '../views/pledge/pledgeGABA.vue'
import PledgeNXX from '../views/pledge/pledgeNXX.vue'
import PledgeRecord from '../views/pledge/pledgeRecord.vue'
import Lender from '../views/pledge/view/lender.vue'
import LenderJoin from '../views/pledge/view/lenderJoin.vue'
import LenderDetails from '../views/pledge/view/lenderDetails.vue'

Vue.use(VueRouter)

const routes = [
	// {
	// 	path: '/',
	// 	name: 'home',
	// 	component: Home,
	// 	meta: {
	// 		tabbar: true
	// 	}
	// },
	{
		path: '/',
		name: 'income',
		component: income,
		meta: {
			tabbar: true
		}
	},
	{
		path: '/my',
		name: 'my',
		component: My,
		meta: {
			tabbar: true
		}
	},
	{
		path: '/exchange',
		name: 'exchange',
		component: Exchange,
		children: [
			{
				path: '/exchange/change',
				name: 'exchange',
				component: Change,
				meta: {
					record: false,
					tabbar: true
				}
			},
			{
				path: '/exchange/fund',
				name: 'exchange',
				component: Fund,
				meta: {
					record: true,
					tabbar: true
				}
			}
		]
	},
	{
		path: '/exchange/view/fundRecord',
		name: 'fundRecord',
		component: FundRecord
	},
	{
		path: '/my/view/myLnvitationList',
		name: 'myLnvitationList',
		component: MyLnvitationList
	},
	{
		path: '/my/view/About',
		name: 'About',
		component: About
	},
	{
		path: '/join',
		name: 'join',
		component: Join
	},
	{
		path: '/record',
		name: 'record',
		component: Record,
		children: [
			{
				path: '/record/joinRecord',
				name: 'joinRecord',
				component: JoinRecord,
				meta: {
					record: false
				}
			},
			{
				path: '/record/incomeRecord',
				name: 'incomeRecord',
				component: IncomeRecord,
				meta: {
					record: true
				}
			}
		]
	},
	{
		path: '/pledgeGuide',
		name: 'pledge',
		component: PledgeGuide,
		meta: {
			tabbar: true
		}
	},
	{
		path: '/pledge',
		name: 'pledge',
		component: Pledge,
		meta: {
			tabbar: true
		},
		children: [
			{
				path: '/pledgeGABA',
				name: 'pledge',
				component: PledgeGABA,
				meta: {
					record: false,
					tabbar: true
				}
			},
			{
				path: '/pledgeNXX',
				name: 'pledge',
				component: PledgeNXX,
				meta: {
					record: true,
					tabbar: true
				}
			}
		]
	},
	{
		path: '/pledgeRecord',
		name: 'pledgeRecord',
		component: PledgeRecord,
	},
	{
		path: '/lender',
		name: 'pledge',
		component: Lender,
		meta: {
			tabbar: true
		}
	},
	{
		path: '/lenderJoin',
		name: 'pledge',
		component: LenderJoin,
		meta: {
			tabbar: true
		}
	},
	{
		path: '/lenderDetails',
		name: 'pledge',
		component: LenderDetails,
		meta: {
			tabbar: true
		}
	},
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
