<template>
	<div class="box">
		<div class="pledgeGABA_Top">
			<div class="pledgeGABA_TopInfo">
				<div @click="pushYe('./lender')">
					<div>
						<span>{{$t('PledgePool')}}</span>
						<icon name="arrow" />
					</div>
					<span>{{usdtAmount}} USDT</span>
				</div>
				<div @click="pushYe('./pledgeRecord')">
					<div>
						<div>
							<span>{{$t('MyPledge')}}</span>
							<span>{{availPledgeTokenAmount}} GABA</span>
						</div>
					</div>
					<div>
						<div>
							<span>{{$t('PledgeProfit')}}</span>
							<span>{{rewardUSDT}} USDT</span>
						</div>
						<icon name="arrow" />
					</div>
				</div>
			</div>
			<div class="pledgeGABA_Input">
				<div>
					<div><img src="../../assets/pledgeGABA.png"/><span>GABA</span></div>
					<div><img src="../../assets/pledgeUSDT.png"/><span>USDT</span></div>
				</div>
				<div>
					<input :placeholder="$t('PledgeQuantity')" v-model="pledgeAmount" @input="input" />
					<img src="../../assets/pledgeChange.png" />
					<input :placeholder="$t('AcceptingQuantity')" disabled v-model="receiveAmount" />
				</div>
				
				<div>
					<div>{{$t('Available')}} {{gaba}} GABA</div>
					<div>{{$t('RemainingQuantityGABA')}} {{(usdtAmount != '' && changeProps != '') ? Math.floor(usdtAmount / changeProps) : ''}} GABA</div>
				</div>
			</div>
			<div class="pledgeGABA_Fee">
				<div><span></span>{{$t('ServiceCharge')}} {{fee}} USDT</div>
				<div>{{$t('PledgePrice')}}  {{changeProps}} USDT</div>
			</div>
			<div class="pledgeGABA_Button">
				<button @click="Pledging">{{$t('pledge')}}</button>
				<div @click="rule = true">
					<img src="../../assets/details.png" />
					{{$t('PledgeRule')}}
				</div>
			</div>
		</div>
		
		<div class="MyPledgeContract">
			<div class="MyPledgeContractTitle">
				<div></div>
				<span>{{$t('MyPledgeContract')}}</span>
			</div>
			<div class="MyPledgeContractListBox">
				<div class="MyPledgeContractList" v-for="(item,index) in  list" :key="index">
					<div>
						<div>{{$t('ContractNo')}}：{{item.seek}}</div>
						<div>{{$toTime(item.createTime,'Y/M/D H:i')}}</div>
					</div>
					<div>
						<div><span>{{$t('PledgeQuantity')}}</span><span>{{item.pledgeAmount}} GABA</span></div>
						<div><span>{{$t('PledgeValue')}}</span><span>{{item.USDTAmount}} USDT</span></div>
					</div>
					<div>
						<div>
							<span :class="{fontRed:item.active}">{{$t('DeadlineForRenewal')}}  {{$toTime(item.expirationDate,'Y/M/D H:i')}}</span>
							<div v-if="item.state == '1'" @click="Renew = true;selectId = item.seek;selectAmount = item.USDTAmount">{{$t('ContractRenewal')}}</div>
						</div>
						<button class="bule" v-if="item.state == '1'" @click="PledgeRedeemOrder(item.seek,item.USDTAmount)">{{$t('Redemption')}}</button>
						<button class="gray" v-else-if="item.state == '2'">{{$t('Redeemed')}}</button>
						<button class="red" v-else-if="item.state == '3'">{{$t('BreachContract')}}</button>
						<button class="red" v-else-if="item.state == '4'">{{$t('Recycled')}}</button>
					</div>
				</div>
			</div>
		</div>
		
		<popup v-model="Renew">
			<div class="Renew">
				<span>{{$t('ContractRenewal')}}</span>
				<p>{{$t('renewalText')}}</p>
				<div>
					<input type="number" v-model="PledgeRenewOrderAmount" @input="PledgeRenewOrderInput" />
					<div>{{$t('Month')}}</div>
				</div>
				<p>{{$t('ServiceCharge')}} {{PledgeRenewOrderFeeAmount}} USDT</p>
				<button @click="PledgeRenewOrder">{{$t('sure')}}</button>
			</div>
		</popup>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun" 
		:needAuthorization='needAuthorization' 
		:availableAuthorizations="authorizedAccessAmount" 
		:tokenType="tokenType"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		@authorizedAccessInput="getNumber"
		></authorization>
		
		<popup v-model="rule">
			<div class="rule">
				<div>{{$t('PledgeRule')}}</div>
				<p>{{$t('PledgeRuleText01')}}</p>
				<p>{{$t('PledgeRuleText02')}}</p>
				<p>{{$t('PledgeRuleText03')}}</p>
				<p>{{$t('PledgeRuleText04')}}</p>
				<p>{{$t('PledgeRuleText05')}}</p>
				<p>{{$t('PledgeRuleText06')}}</p>
				<p>{{$t('PledgeRuleText07')}}</p>
				<p>{{$t('PledgeRuleText08')}}</p>
				<p>{{$t('PledgeRuleText09')}}</p>
				<p>{{$t('PledgeRuleText10')}}</p>
				<p>{{$t('PledgeRuleText11')}}</p>
				<p>{{$t('PledgeRuleText12')}}</p>
				<p>{{$t('PledgeRuleText13')}}</p>
			</div>
		</popup>
	</div>
</template>

<script>
	import { icon , Popup , Toast } from 'vant';
	import Authorization from '../../components/Authorization.vue';
	export default{
		components:{ icon , Popup , Toast , Authorization },
		data () {
			return {
				Renew: false,
				authorizedAccess: false,
				rule: false,
				usdtAmount: '',
				availPledgeTokenAmount: 0,
				availPledgeUSDTAmount: 0,
				rewardUSDT: 0,
				usdt: '0.00',
				gaba: '0.00',
				changeProps: '',
				minlimits: 0,
				baseOfAmounts: 0,
				pledgeAmount: '',
				receiveAmount: '',
				fee: 0,
				list: [],
				PledgeRenewOrderAmount: '',
				PledgeRenewOrderFeeAmount: 0,
				selectId: '',
				selectAmount: '',
				USDTAuthorizedAccess: 0,
				GABAAuthorizedAccess: 0,
				authorizedAccessAmount: '',
				authorizedAccessNumber: '',
				authorizationEnds: false,
				tokenType: '',
				needAuthorization: '',
				invite: false,
				AvailPledgeOrderCount: 0,
				pledgeOrderUSDTMaxlimit: 0,
				pledgeOrdersTotalUSDTLimit: 0,
				AvailPledgeOrderTotalUSDTAmount: '',
				strat10: 0,
				strat22: 0,
			}
		},
		methods:{
			async init(){
				this.fee = 0;
				this.pledgeAmount = ''
				this.receiveAmount = ''
				this.authorizedAccess = false
				this.Renew = false;
				this.PledgeRenewOrderAmount = '';
				
				this.$contract.pledgeGABA.methods.PledgeStateInfo(this.$address,this.$contract.gabaToken._address).call((err,result)=>{
					// console.log(result)
					// 质押池
					this.usdtAmount = this.$fromWei(result.usdtAmount,2,'Mwei');
					
					//我的质押
					this.availPledgeTokenAmount = this.$fromWei(result.availPledgeTokenAmount,2);
					
					// 佣金
					this.rewardUSDT = this.$fromWei(result.rewardUSDT,2,'Mwei');
					
					// 已履约USDT总量
					this.availPledgeUSDTAmount = this.$fromWei(result.availPledgeUSDTAmount,'','Mwei');
				})
				
				//当前0点时间戳
				var strat = new Date(new Date().toLocaleDateString()).getTime()/1000;
				this.strat10 = strat + (3600 * 10);
				this.strat22 = strat + (3600 * 20);
				
				// 可用代币数量
				this.usdt = this.$fromWei(await this.$contract.usdtToken.methods.balanceOf(this.$address).call(),'','Mwei')
				this.gaba = this.$numFixed(this.$fromWei(await this.$contract.gabaToken.methods.balanceOf(this.$address).call()),2)
				
				var PledgeItemList = await this.$contract.pledgeGABA.methods.PledgeItemList().call()
				for (let index in PledgeItemList.contractAddresses) {
					if(PledgeItemList.contractAddresses[index] == this.$contract.gabaToken._address){
						this.changeProps = this.$fromWei(PledgeItemList.changeProps[index],'','Mwei');
						this.minlimits = this.$fromWei(PledgeItemList.minlimits[index]);
						this.baseOfAmounts = this.$fromWei(PledgeItemList.baseOfAmounts[index]);
					}
				}
				
				// 用户单笔最高可借多少USDT
				this.pledgeOrderUSDTMaxlimit = this.$fromWei(await this.$contract.pledgeGABA.methods.pledgeOrderUSDTMaxlimit().call(),'','Mwei')
				
				// 已履行USDT总量最高限制
				this.pledgeOrdersTotalUSDTLimit = this.$fromWei(await this.$contract.pledgeGABA.methods.pledgeOrdersTotalUSDTLimit().call(),'','Mwei')
				
				// 用户有效订单
				this.AvailPledgeOrderCount = await this.$contract.pledgeGABA.methods.AvailPledgeOrderCount(this.$address).call()
				
				// 是否绑定关系
				var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
				this.invite = parseInt(invite,16) ? false : true ;
				
				// gaba授权额度
				this.GABAAuthorizedAccess = this.$fromWei(await this.$contract.gabaToken.methods.allowance(this.$address,this.$contract.pledgeGABA._address).call({from:this.$address}))
				
				// USDT授权额度
				this.USDTAuthorizedAccess = this.$fromWei(await this.$contract.usdtToken.methods.allowance(this.$address,this.$contract.pledgeGABA._address).call({from:this.$address}),'','Mwei')
				
				// 我的质押合同
				this.$contract.pledgeGABA.methods.PledgedOrderIDAndState(this.$address).call((err,result)=>{
					this.list = [];
					var getTime = new Date().getTime()/1000;
					result.ids.forEach(async item=>{
						var list = await this.$contract.pledgeGABA.methods.PledgeOrderInfo(item).call();
						if(list.pledgeERC20TokenInc != this.$contract.gabaToken._address) return 
						
						// 是否违约
						if(list.expirationDate > getTime){
							var Expired = false
						}else{
							var Expired = true
						}
						
						// 截止时间是否还剩一天
						if(list.expirationDate - getTime <= 86400){
							var active = true
						}else{
							var active = false
						}
						
						this.list.unshift({
							seek: list.seek,
							address: list.owner,
							state: list.state,
							createTime: list.createTime,
							expirationDate: list.expirationDate,
							pledgeAmount: this.$fromWei(list.pledgeAmount),
							USDTAmount: this.$fromWei(list.USDTAmount,'','Mwei'),
							Expired: Expired,
							active:  active,
						})
						
						this.list.sort(function(a,b){
							return a.seek - b.seek
						})
						
						// console.log(this.list)
					})
				})
			},
			input(){
				this.pledgeAmount = this.pledgeAmount.replace(/[^\d]/g,'')
				this.receiveAmount = Math.floor(this.pledgeAmount * this.changeProps);
				this.fee = this.$upFixed(this.receiveAmount * 0.05,2)
				if(this.pledgeAmount == ''){
					this.receiveAmount = ''
					this.fee = 0
				}else if(this.pledgeAmount > this.gaba){
					this.pledgeAmount = this.gaba
					this.receiveAmount = Math.floor(this.pledgeAmount * this.changeProps);
					this.fee = this.$upFixed(this.receiveAmount * 0.05,2)
				}
			},
			Pledging(){  //质押
				var pledgeAmount = Number(this.pledgeAmount);
				var receiveAmount = Number(this.receiveAmount);
				var fee = Number(this.fee);
				
				if(new Date().getTime()/1000 < this.strat10 || new Date().getTime()/1000 > this.strat22){
					return Toast.fail(this.$t('timeStrat'))
				}else if(this.invite){
					return Toast.fail(this.$t('tips3'))
				}else if(pledgeAmount < this.minlimits || pledgeAmount % this.baseOfAmounts != 0 ){
					return Toast.fail(this.$t('minimum')+this.minlimits+this.$t('And')+this.baseOfAmounts+this.$t('InMultiplesOf'))
				}else if(receiveAmount > Number(this.usdtAmount)){
					return Toast.fail(this.$t('InsufficientPledgePool'))
				}else if(this.AvailPledgeOrderCount >= 20){
					return Toast.fail(this.$t('PledgeWarningAlertText02')+'20')
				}else if((this.availPledgeUSDTAmount + receiveAmount) > this.pledgeOrdersTotalUSDTLimit){
					return Toast.fail(this.$t('PledgeWarningAlertText03')+this.pledgeOrdersTotalUSDTLimit+','+this.$t('YouStillHave')+(this.pledgeOrdersTotalUSDTLimit - this.availPledgeUSDTAmount))
				}else if(receiveAmount > this.pledgeOrderUSDTMaxlimit){
					return Toast.fail(this.$t('PledgeWarningAlertText04')+this.pledgeOrderUSDTMaxlimit)
				}else if(pledgeAmount > this.GABAAuthorizedAccess){
					this.tokenType = 'GABA'
					this.needAuthorization = pledgeAmount
					this.authorizedAccessAmount = this.GABAAuthorizedAccess
					return this.authorizedAccess = true;
				}
				// else if(fee > this.USDTAuthorizedAccess){
				// 	this.tokenType = 'USDT'
				// 	this.needAuthorization = this.fee
				// 	this.authorizedAccessAmount = this.USDTAuthorizedAccess
				// 	return this.authorizedAccess = true;
				// }
				console.log(this.$contract.gabaToken._address,this.$toWei(this.pledgeAmount.toString()))
				this.$contract.pledgeGABA.methods.Pledging(this.$contract.gabaToken._address,this.$toWei(this.pledgeAmount.toString())).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			authorizedAccessFun(){  //授权gaba
				if(this.tokenType == 'GABA'){					
					this.$contract.gabaToken.methods.approve(this.$contract.pledgeGABA._address,this.$web3.utils.toWei(this.authorizedAccessNumber.toString(),'ether')).send({
						from : this.$address
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						this.init()
						this.authorizationEnds = true;
					})
				}else{
					this.$contract.usdtToken.methods.approve(this.$contract.pledgeGABA._address,this.$web3.utils.toWei(this.authorizedAccessNumber.toString(),'Mwei')).send({
						from : this.$address
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err)
					}).on('receipt' , result=>{
						this.init()
					})
				}
			},
			PledgeRedeemOrder(id,usdtAmount){  //赎回
				if(usdtAmount > this.USDTAuthorizedAccess){
					// return Toast.fail('授权额度不足')
					this.tokenType = 'USDT'
					this.needAuthorization = usdtAmount
					this.authorizedAccessAmount = this.USDTAuthorizedAccess					return this.authorizedAccess = true;
				}
				this.$contract.pledgeGABA.methods.PledgeRedeemOrder(id).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			PledgeRenewOrder(){	 //续约
				if(this.PledgeRenewOrderAmount < 1 || this.PledgeRenewOrderAmount > 12 || this.PledgeRenewOrderAmount % 1 != 0){
					return Toast.fail(this.$t('PledgeWarningAlertText05'))
				}else if(this.PledgeRenewOrderFeeAmount > this.USDTAuthorizedAccess){
					this.tokenType = 'USDT'
					this.needAuthorization = this.PledgeRenewOrderFeeAmount
					this.authorizedAccessAmount = this.USDTAuthorizedAccess
					return this.authorizedAccess = true;
				}
				this.$contract.pledgeGABA.methods.PledgeRenewOrder(this.selectId,this.PledgeRenewOrderAmount).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			pushYe(lu){
				localStorage.setItem('tokenType','GABA');
				this.$router.push(lu);
			},
			PledgeRenewOrderInput(){
				this.PledgeRenewOrderFeeAmount = this.$upFixed(this.selectAmount * (this.PledgeRenewOrderAmount * 2) / 100,2)
			},
			getNumber(value){
				this.authorizedAccessNumber = value;
			},
			end(){
				this.authorizedAccess = false;
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

