import Vue from 'vue';
import VueI18n  from 'vue-i18n';

Vue.use(VueI18n);

//引入配置文件
import zh from './zh.js';
import en from './en.js';

//创建i18n实例
const i18n = new VueI18n({
	//设置默认语言
	locale: localStorage.getItem('locale') || 'zh',
	// 添加多语言
	messages: {
		zh,en
	}
})

export default i18n