<template>
	<div class="box">
		<div class="title">{{$t('InvitationList')}}</div>
		<div class="switchState">
			<div>{{$t('MyPartner')}}</div>
			<!-- <div>{{$t('state')}}</div> -->
			<div>{{$t('CurrentEntry')}}/GABA</div>
			<div>{{$t('CommunityPlayer')}}</div>
		</div>
		<div class="contentList">
			<div class="list" v-for="(item, index) of list" :key="index">
				<div>{{ item.partner }}</div>
				<div>{{ item.totalJoin }}</div>
				<div>{{ item.member }}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			list: []
		}
	},
	methods: {
		init() {
			// if (this.$cache.myLnvitationList) {
			// 	this.list = this.$cache.myLnvitationList.list
			// 	this.getData()
			// } else {
			// 	this.$cache.myLnvitationList = {}
			// 	this.getData()
			// }
		},
		getData() {
			this.$contract.recommend.methods.RecommendList(this.$address, 0).call((err, res) => {
				if (!err && res.list.length > 0) {
					this.list = []
					res.list.forEach(async address => {
						console.log(address)
						let partner = await this.$contract.recommend.methods.AddressToShortCode(address).call(),
							totalJoin = this.$fromWei(await this.$contract.mainContract.methods.CurrentDepositedTotalCount(address).call()),
							member = await this.$contract.recommend.methods.TeamMemberTotal(address).call()
						console.log(totalJoin)
						if (Number(partner) === 0) {
							partner = this.$t('NoCode')
						} else {
							partner = this.$web3.utils.hexToString(partner)
						}

						let row = {
							partner: partner,
							totalJoin: totalJoin,
							member: member
						}
						
						this.list.push(row)
						// this.$cache.myLnvitationList.list.push(row)
					})
				}
			})
		}
	},
	mounted() {
		this.init()
		this.getData()
	}
}
</script>

<style lang="scss" scoped>
	.box{
		width: 100vw;
		height: 100vh;
		display: flex;
		flex-direction: column;
		color: #fff;
		
		.title{
			padding: 36px 0;
			font-size: 32px;
		}
		
		.switchState,.contentList{
			font-size: 24px;
		}
		
		.switchState{
			width: 100%;
			display: flex;
			padding: 18px 0;
			border-top: 1px solid #c4c4c4;
			border-bottom: 1px solid #c4c4c4;
			
			div{
				flex: 1;
				text-align: center;
			}
		}
		
		.contentList{
			flex: 1;
			width: 100%;
			overflow-y: scroll;
			
			.list{
				display: flex;
				margin-top: 20px;
				
				div{
					flex: 1;
					text-align: center;
				}
			}
		}
	}
</style>
