<template>
	<div class="box">
		<div class="exchangeFrom">
			<div class="Description">{{$t('ProtocolExchange')}}&nbsp;1 GABA = {{ONEF_Prop}} ONEF</div>
			<div class="Description">{{$t('RemainingQuota')}}&nbsp;{{ isChangeable ? RemainingONEF : 0 }} GABA</div>
			
			<div class="exchangeInputBox">
				<input type='number' :placeholder="$t('ExchangeQuota')" v-model="exchangeNumber" @input="exchangeInput" />
				<div>
					<img src="../../assets/exchangeGABA.png" />
					<span>GABA</span>
				</div>
			</div>
			
			<div class="exchangeBalance">
				<div>					
					<span>{{$t('Available')}}</span>
					<span>{{gaba}}</span>
					<span>GABA</span>
				</div>
			</div>
			
			<div class="exchangeInputBox">
				<input type='number' :placeholder="$t('ReceivingQuota')" disabled="disabled" v-model="exchangeONEF" />
				<div>
					<img src="../../assets/exchangeONEF.png" />
					<span>ONEF</span>
				</div>
			</div>
			
			<div class="exchangeBalance">
				<div>
					<span>{{$t('Available')}}</span>
					<span>{{onef}}</span>
					<span>ONEF</span>
				</div>
				<div @click="changeDetails=true">
					<img src="../../assets/details.png"/>
					<span>{{$t('GettoKnowDetails')}}</span>
				</div>
			</div>
			
			<button @click="exchange">{{$t('exchange')}}</button>
		</div>
		
		<div class="Statistics">
			<div class="StatisticsBox">
				<div>
					<p>{{exchangePool}}</p>
					<span>{{$t('ExchangePool')}}/ONEF</span>
				</div>
				<div @click="WithdrawLockedGaba">
					<p>{{lockupPool}}</p>
					<span>{{$t('LockPool')}}/GABA</span>
				</div>
			</div>
		</div>
		
		<div class="exchangeRecord">
			<p>{{$t('ExchangeRecord')}}</p>
			<div class="exchangRecordContent">
				<div class="exchangRecordList" v-for="(item,index) in exchangRecordList" :key="index">
					<div><span class="green">{{$t('SuccessfulExchange')}}</span><span>+{{$fromWei(item.onef)}} ONEF</span></div>
					<div><span>{{$toTime(item.time,'M.D H:i')}}</span><span>-{{$fromWei(item.gaba)}} GABA</span></div>
				</div>
				<!-- <div class="exchangRecordList">
					<div><span class="bule">{{$t('Transacting')}}</span><span>+150 ONEF</span></div>
					<div><span>03.05 17:50</span><span>-5 GABA</span></div>
				</div>
				<div class="exchangRecordList">
					<div><span class="green">{{$t('SuccessfulExchange')}}</span><span>+150 ONEF</span></div>
					<div><span>03.05 17:50</span><span>-5 GABA</span></div>
				</div>
				<div class="exchangRecordList">
					<div><span class="red">{{$t('FailedExchange')}}</span><span>+150 ONEF</span></div>
					<div><span>03.05 17:50</span><span>-5 GABA</span></div>
				</div> -->
				<div class="noRecord" v-if="Record">
					<img src="../../assets/noRecord.png"/>
					<span>{{$t('NoTransaction')}}</span>
				</div>
			</div>
		</div>
		
		<authorization 
		:show="authorizedAccess" 
		:click="onefAuthorization" 
		:needAuthorization='exchangeNumber' 
		:availableAuthorizations="authorizedAccessAmount" 
		:tokenType="'GABA'"
		@authorizedAccessInput="getNumber"></authorization>
		
		<popup v-model="changeDetails" >
			<div class="changeDetails">
				<p>{{$t('AboutExchange')}}</p>
				<p>{{$t('ExchangeDetailsText1')}}</p>
				<!-- <div><span>1</span><span>{{$t('ExchangeDetailsText2')}}</span></div>
				<div><span>2</span><span>{{$t('ExchangeDetailsText3')}}</span></div>
				<div><span>3</span><span>{{$t('ExchangeDetailsText4')}}</span></div> -->
				<img @click="changeDetails=false" src="../../assets/out.png" />
			</div>
		</popup>
	</div>
</template>

<script>
	import { Popup , Toast } from 'vant';
	import axios from 'axios';
	import Authorization from '../../components/Authorization.vue';
	
	export default {
		components: { Popup , Toast , Authorization },
		data(){
			return{
				authorizedAccess: false,
				Record: false,
				changeDetails: false,
				invite: false,
				isChangeable: true,
				ONEF_Prop: 0,
				exchangeNumber: '',
				exchangeONEF: '',
				RemainingONEF: 0,
				gaba: 0,
				onef: 0,
				exchangePool: 0,
				lockupPool: 0,
				exchangRecordList: [],
				authorizedAccessAmount: 0,
				authorizedAccessNumber: '',
			}
		},
		methods:{
			async init(){
				this.authorizedAccess = false;
				
				this.ONEF_Prop = Number(await this.$contract.onefTokenChanger.methods.GetGABAToONEFChangeRate().call())
				this.gaba = this.$fromWei(await this.$contract.gabaToken.methods.balanceOf(this.$address).call())
				this.onef = this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$address).call())
				
				// 读取ONEF当前轮次信息
				this.$contract.onefTokenChanger.methods.ChangeStateInfo().call((err,result)=>{
					var currentLockedAmount = this.$fromWei(result.currentLockedAmount);
					var changeQuotaMaxLimit = this.$fromWei(result.changeQuotaMaxLimit);
					
					//锁定池
					this.lockupPool = Math.floor(currentLockedAmount)
					
					//剩余额度
					this.RemainingONEF = changeQuotaMaxLimit - currentLockedAmount;
					
					//兑换状态 true = 正常兑换   false = 暂停
					this.isChangeable = result.isChangeable
					
					
					// this.exchangePool = this.RemainingONEF * this.ONEF_Prop;
				})
				
				// 兑换池
				this.exchangePool = this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$contract.onefTokenChanger._address).call());
				// console.log(this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$contract.financialPlan._address).call()))
				// console.log(this.$fromWei(await this.$contract.financialPlan.methods.CurrentDepositedONEFTotalAmount().call()))
				// console.log(this.RemainingONEF)
				
				// ONEF是否授权
				var authorizedAccess = await this.$contract.gabaToken.methods.allowance(this.$address,this.$contract.onefTokenChanger._address).call({from:this.$address})
				this.authorizedAccessAmount = this.$fromWei(authorizedAccess)
				
				//是否绑定关系
				var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
				this.invite = parseInt(invite,16) ? false : true ;
				
			},
			exchangeInput(){
				var value = Number(this.exchangeNumber);
				if(value > this.gaba){
					value = this.gaba
					this.exchangeNumber = this.gaba
				}else if(value > this.RemainingONEF){
					value = this.RemainingONEF
					this.exchangeNumber = this.RemainingONEF
				}
				this.exchangeONEF = value * this.ONEF_Prop
			},
			exchange(){
				if(!this.isChangeable){
					return Toast.fail(this.$t('pauseExchange'))
				}else if(this.exchangeNumber == 0 || this.exchangeNumber == '0' || this.exchangeNumber == ''){
					return Toast.fail(this.$t('tips4'))
				}else if(Number(this.exchangeNumber) % 1 != 0){
					return Toast.fail(this.$t('tips5'))
				}else if(this.invite){
					return Toast.fail(this.$t('tips3'))
				}else if(this.exchangeNumber > this.authorizedAccessAmount){
					return this.authorizedAccess = true;
				}
				this.$contract.onefTokenChanger.methods.DoChange(this.$toWei(this.exchangeNumber.toString())).send({
					from: this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , console.error)
				.on('receipt' , result=>{
					console.log(result)
					this.init()
					this.exchangeNumber = ''
					this.exchangeONEF = ''
				})
			},
			onefAuthorization(){
				this.$contract.gabaToken.methods.approve(this.$contract.onefTokenChanger._address,this.$web3.utils.toWei(this.authorizedAccessNumber.toString(),'ether')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			WithdrawLockedGaba(){
				// this.$contract.onefTokenChanger.methods.WithdrawLockedGaba().send({
				// 	from: this.$address
				// }).on('transactionHash' , txHash =>{
				// 	console.log(txHash)
				// }).on('error' , console.error)
				// .on('receipt' , result=>{
				// 	this.init()
				// })
			},
			getNumber(value){
				this.authorizedAccessNumber = value;
			}
		},
		mounted() {
			this.init()
			
			axios.get('https://api-cn.etherscan.com/api',{
				params: {
					"module":'logs',
					"action":'getLogs',
					"fromBlock":9514338,
					"toBlock":'latest',
					"address":this.$contract.onefTokenChanger._address,
					"topic0":'0xe61e14e1a028cf94b2dbc279149e0235141f23775d86e9a4e79faf261859c034',
					"topic1": this.$web3.eth.abi.encodeParameter('address',this.$address),
					// "topic3": this.$web3.eth.abi.encodeParameter('uint16','100'),
					"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU',
				}
			}).then( result => {
				// console.log(result)
				if(result.data.result.length == 0){
					this.Record = true
				}
				result.data.result.forEach( item => {
					var data = this.$web3.eth.abi.decodeParameters(['uint256','uint256','uint256'],item.data);
					this.exchangRecordList.unshift({
						time: data[0],
						gaba: data[1],
						onef: data[2],
					})
					// console.log(this.exchangRecordList)
				})
			}).catch( err => {
				console.log(err)
			});
		}
	}
</script>

<style lang="scss" scoped>
	.exchangeFrom{
		width: 100%;
		height: 593px;
		padding: 38px 36px;
		background-color: rgba($color: #2A78C8, $alpha: 0.61);
		font-size: 28px;
		border-radius: 10px;
		
		&>.Description{
			width: 100%;
			text-align: left;
			margin-bottom: 10px;
		}
		
		&>.exchangeInputBox{
			width: 100%;
			height: 100px;
			border-radius: 20px;
			border: 2px solid #3A9CFF;
			display: flex;
			margin-bottom: 14px;
			
			&>input{
				width: 420px;
				height: 100%;
				font-size: 32px;
				padding-left: 22px;
				background-color: transparent;
				border: none;
			}
			
			&>input::placeholder{
				color: #A6A6A6;
			}
			
			&>div{
				border-left: 2px solid #3A9CFF;
				flex: 1;
				display: flex;
				align-items: center;
				justify-content: center;
				
				&>img{
					width: 48px;
					height: 44px;
					margin-right: 26px;
				}
			}
		}
		
		&>.exchangeBalance{
			display: flex;
			justify-content: space-between;	
			align-items: center;
			margin-bottom: 26px;
			
			&>div:nth-child(1){
				&>span:nth-child(2){
					margin: 0 5px;
				}
			}
			
			&>div:nth-child(2){
				color: #8E99A4;
				display: flex;
				align-items: center;
				line-height: 1.26;
				
				&>img{
					width: 36px;
					margin-right: 8px;
				}
			}
		}
		
		&>button{
			width: 340px;
			height: 72px;
			border: none;
			background-color: #3F8DE0;
			border-radius: 5px;
		}
	}
	
	.Statistics{
		margin: 10px 0;
		// margin-bottom: 45px;
		
		&>.StatisticsBox{
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			
			&>div{
				width: 50%;
				height: 240px;
				
				background-size: 100%;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				font-size: 28px;
				
				&>p{
					font-size: 40px;
				}
			}
			
			&>div:nth-child(1){
				background-image: url(../../assets/StatisticsBoxTopLeft.png);
			}
			
			&>div:nth-child(2){
				background-image: url(../../assets/StatisticsBoxTopRight.png);
			}
		}
	}
	
	.exchangeRecord{
		width: 100%;
		height: 543px;
		background-color: rgba($color: #4A7EB5, $alpha: 0.39);
		border-radius: 10px;
		font-size: 28px;
		padding: 0 35px;
		display: flex;
		flex-direction: column;
		position: relative;
		
		&>p{
			padding: 10px 0 8px;
		}
		
		&>.exchangRecordContent{
			flex: 1;
			overflow-y: scroll;
			
			&>.exchangRecordList{
				font-size: 24px;
				margin-bottom: 10px;
				
				&>div{
					width: 100%;
					display: flex;
					justify-content: space-between;
				}
			}
			
			&>.noRecord{
				font-size: 24px;
				color: #758D9C;
				display: flex;
				flex-direction: column;
				align-items: center;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				
				&>img{
					width: 67px;
					height: 85px;
				}
			}
		}
		
		.bule{
			color: #12B3FE;
		}
		
		.green{
			color: #19C580;
		}
		
		.red{
			color: #C56363;
		}
	}
	
	
	.van-popup--center{
		background: transparent;
	}
	
	.changeDetails{
		width: 708px;
		height: 712px;
		background-color: rgba(16,79,153,.89);
		border-radius: 10px;
		font-size: 28px;
		padding: 42px 36px;
		text-align: left;
		position: relative;
		overflow-y: scroll;
		
		&>p:nth-child(1){
			margin-bottom: 20px;
		}
		
		&>div{
			display: flex;
			// line-height: 1;
			
			&>span:nth-child(1){
				display: inline-block;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				border: 1px solid #fff;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 15px;
				margin-top: 6px;
				padding-top: 1px;
			}
			
			&>span:nth-child(2){
				flex: 1;
			}
		}
		
		&>img{
			position: absolute;
			width: 40px;
			height: 40px;
			top: 42px;
			right: 42px;
		}
	}
</style>
