<template>
	<div class="van-tabbar-page box">
		<div class="ContractInfo">
			<div class="ContractInfoNumber">
				<div>
					<span>{{details.totalOrderCount}}</span>
					<span>{{$t('numberContracts')}}</span>
				</div>
				<div>
					<div>
						<span class="green">{{details.nomalOrderCount}}</span>
						<span>{{$t('InPerformance')}}</span>
					</div>
					<div>
						<span>{{details.successedOrderCount}}</span>
						<span>{{$t('Redeemed')}}</span>
					</div>
					<div>
						<span class="red">{{details.expOrderCount}}</span>
						<span>{{$t('brokenContract')}}</span>
					</div>
				</div>
			</div>
			<div>
				<span>{{$t('Breach')}}</span>
				<div>
					<div>
						<div v-if="pldegeType == 'NXX'">NXX {{BreachNXX}}</div>
						<div v-else>GABA {{BreachGABA}}</div>
					</div>
					<button @click="LenderCheckExpOrdersWithLenderID">{{$t('receive')}}</button>
				</div>
			</div>
		</div>
		
		<div class="MyLenderContract">
			<div class="MyLenderContractTitle">
				<div></div>
				<span>{{$t('MyPledgeReleaseContract')}}</span>
			</div>
			<div class="MyLenderContractListBox">
				<div class="MyLenderContractList" v-for="(item,index) in list" :key='index'>
					<div>
						<div>{{$t('ContractNo')}}：{{item.seek}}</div>
						<div>{{$toTime(item.createTime,'Y/M/D H:i')}}</div>
					</div>
					<div>
						<div>
							<div><span>{{$t('PledgeQuantity')}}</span><span>{{item.pledgeAmount}} {{item.tokenType}}</span></div>
							<div><span>{{$t('PledgeValue')}}</span><span>{{item.USDTAmount}} USDT</span></div>
						</div>
						<div>
							{{$t('Pledgor')}} {{item.address.substr(0,6)+'****'+item.address.substr(-6,6)}}
						</div>
					</div>
					<div>
						<span>{{$t('DeadlineForRenewal')}}  {{$toTime(item.expirationDate,'Y/M/D H:i')}}</span>
						<div v-if="item.state == '1'" class="bule">{{$t('InPerformance')}}</div>
						<div v-else-if="item.state == '2'" class="gray">{{$t('Redeemed')}}</div>
						<div v-else-if="item.state == '3'" class="red">{{$t('BreachContract')}}</div>
						<div v-else-if="item.state == '4'" class="red">{{$t('Recycled')}}</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { Popup , Toast } from 'vant';
	import NXXToken from '../../../static/NXXToken.json';
	export default{
		components:{ Popup , Toast },
		data () {
			return {
				pledgeContractType: '',
				details: '',
				BreachGABA: 0,
				BreachNXX: 0,
				list: [],
				id: '',
				pldegeType: '',
			}
		},
		methods:{
			async init(){
				this.list = [];
				this.id = this.$route.query.id;
				
				//获取上方所有数据
				this.details = await this.pledgeContractType.LenderOrdersState(this.id).call();
				for (let index in this.details.expPledgeContracts) {
					var test = new this.$web3.eth.Contract(NXXToken.abi, this.details.expPledgeContracts[index]);
					var name = await test.methods.symbol().call()
					if(name == 'GABA'){
						this.BreachGABA = this.$numFixed(this.$fromWei(this.details.expAmounts[index]),2)
					}else{
						this.BreachNXX = this.$numFixed(this.$fromWei(this.details.expAmounts[index]),2)
					}
				}
				
				// 获取违约合同质押资产
				this.pledgeContractType.LenderCheckExpOrdersWithLenderID(this.id).call((err,result)=>{
					console.log(err)
					console.log(result)
				})
				
				// 获取我的放押合同-paymentedOrderIDS订单编号
				this.LenderInfo = await this.pledgeContractType.LenderInfo(this.id).call();
				for (let index in this.LenderInfo.paymentedOrderIDS) {
					// 获取我的质押订单详情
					var list = await this.pledgeContractType.PledgeOrderInfo(this.LenderInfo.paymentedOrderIDS[index]).call();
					var USDTAmountList = await this.pledgeContractType.OrderPaymentedRelations(this.LenderInfo.paymentedOrderIDS[index]).call();
					var PledgeItemList = await this.pledgeContractType.PledgeItemList().call();
					
					for (let item in USDTAmountList.lenderIDS) {
						if(USDTAmountList.lenderIDS[item] == this.id){
							var USDTAmount = USDTAmountList.lenderPaymentedAmounts[item]
						}
					}
					//获取代币名称
					var gabaToken = new this.$web3.eth.Contract(NXXToken.abi, list.pledgeERC20TokenInc);
					var tokenType = await gabaToken.methods.symbol().call();

					this.list.unshift({
						seek: list.seek,
						address: list.owner,
						state: list.state,
						createTime: list.createTime,
						expirationDate: list.expirationDate,
						pledgeAmount: Math.floor(this.$fromWei(list.pledgeAmount) * this.$fromWei(USDTAmount,'','Mwei') / this.$fromWei(list.USDTAmount,'','Mwei')),
						USDTAmount: this.$fromWei(USDTAmount,'','Mwei'),
						tokenType: tokenType,
						idPrefix: (Number(list.seek)+1) >= 1000 ? this.$toTime(list.createTime,'YMD')+(Number(list.seek)+1) : (Number(list.seek)+1) >= 100 ? this.$toTime(list.createTime,'YMD')+'0'+(Number(list.seek)+1) : (Number(list.seek)+1) >= 10 ? this.$toTime(list.createTime,'YMD')+'00'+(Number(list.seek)+1) : this.$toTime(list.createTime,'YMD')+'000'+(Number(list.seek)+1)
					})
					
					this.list.sort(function(a,b){
						return b.createTime - a.createTime
					})
				}
			},
			LenderCheckExpOrdersWithLenderID(){
				if(this.BreachGABA <= 0 && this.BreachNXX <= 0){
					return Toast.fail(this.$t('NoDefaultBalance'))
				}
				this.pledgeContractType.LenderCheckExpOrdersWithLenderID(this.id).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			}
		},
		mounted() {
			if(localStorage.getItem('tokenType') == 'GABA'){
				this.pledgeContractType = this.$contract.pledgeGABA.methods
				this.pldegeType = 'GABA'
			}else{
				this.pledgeContractType = this.$contract.pledgeNXX.methods
				this.pldegeType = 'NXX'
			}
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		padding: 20px 26px 0;
		
		.ContractInfo{
			width: 100%;
			height: 356px;
			background-color: rgba($color: #2A78C8, $alpha: 0.61);
			border-radius: 10px;
			padding: 22px 16px 0;
			
			.ContractInfoNumber{
				display: flex;
				padding-bottom: 18px;
				border-bottom: 1px dotted #020C1E;
				
				&>div:nth-child(1){
					width: 194px;
					height: 163px;
					font-size: 28px;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					
					&>span:nth-child(1){
						font-size: 42px;
						margin-bottom: 20px;
					}
				}
				
				&>div:nth-child(2){
					flex: 1;
					display: flex;
					justify-content: space-between;
					border-left: 1px dotted #020C1E;
					
					&>div{
						flex: 1;
						font-size: 28px;
						display: flex;
						flex-direction: column;
						align-items: center;
						justify-content: center;
						
						&>span:nth-child(1){
							font-size: 42px;
							margin-bottom: 20px;
						}
					}
				}
				
				
				.green{
					color: #19C580;
				}
				
				.red{
					color: #DF6464;
				}
				
			}
			
			&>div:nth-child(2){
				font-size: 28px;
				text-align: left;
				margin-top: 16px;
				padding: 0 12px 0 38px;
				
				&>div{
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;

					&>div{
						margin-top: 12px;
					}
					
					&>button{
						width: 206px;
						height: 74px;
						background-color: #3A9CFF;
						border-radius: 10px;
						border: none;
						font-size: 40px;
						color: #E7EAF9;
					}
				}
			}
		}
	}
	
	.MyLenderContract{
		margin-top: 20px;
		
		&>.MyLenderContractTitle{
			display: flex;
			align-items: center;
			font-size: 32px;
			margin-bottom: 14px;
			
			&>div{
				width: 6px;
				height: 36px;
				background-color: #3A9CFF;				
				margin-right: 12px;
			}
		}
		
		&>.MyLenderContractListBox{
			.MyLenderContractList{
				width: 100%;
				height: 214px;
				background-color: rgba($color: #1C528A, $alpha: 0.67);
				border-radius: 10px;
				margin-bottom: 16px;
				display: flex;
				flex-direction: column;
				
				&>div:nth-child(1){
					display: flex;
					justify-content: space-between;
					align-items: center;
					color: #8E99A4;
					font-size: 24px;
					padding: 9px  18px 9px 26px;
				}
				
				&>div:nth-child(2){
					width: 100%;
					height: 96px;
					font-size: 28px;
					background-color: rgba($color: #011740, $alpha: 0.59);
					display: flex;
					flex-direction: column;
					justify-content: center;
					padding: 0 16px 0 26px;
					position: relative;
					
					&>div:nth-child(1){
						display: flex;
						justify-content: space-between;
						
						&>div{
							
							&>span:nth-child(1){
								margin-right: 26px;
							}
						}
					}
					
					&>div:nth-child(2){
						text-align: left;
						font-size: 24px;
						color: #8E99A4;
					}
				}
				
				&>div:nth-child(3){
					flex: 1;
					display: flex;
					align-items: center;
					justify-content: space-between;
					font-size: 28px;
					padding: 0 16px 0 26px;
					
					div{
						width: 128px;
						height: 40px;
						border-radius: 100px;
						font-size: 24px;
						border: none;
						display: flex;
						align-items: center;
						justify-content: center;
					}
					
					.bule{
						background-color: #3A9CFF;
					}
					
					.gray{
						background-color: #537C95;
					}
					
					.red{
						background-color: #DF6464;
					}
				}
			}
		}
	}
</style>
