<template>
	<div class="van-tabbar-page box">
		<div class="lenderTop">
			<div>
				<div>
					<span>{{$t('MyPledgeRelease')}}</span>
					<div>{{totalAmounts}} USDT</div>
				</div>
				<div>
					<span>{{$t('Remaining')}}</span>
					<div>{{delteAmounts}} USDT</div>
				</div>
			</div>
			<div @click="$router.push('./lenderJoin')">
				<img src='../../../assets/lenderBox.png' />
				<span>{{$t('IPledge')}}</span>
				<icon name='arrow' ></icon>
			</div>
		</div>
		
		<div class="MyLenderContract">
			<div class="MyLenderContractTitle">
				<span :class="{active:active == 1}" @click="active = 1">{{$t('MyLenderOrder')}}</span>
				<span :class="{active:active == 2}" @click="active = 2">{{$t('ListOfPools')}}</span>
			</div>
			<div class="MyLenderContractListBox" v-if="active == 1">
				<div class="MyLenderContractList" v-for="(item,index) in LenderList" :key='index' @click="$router.push({path:'./lenderDetails',query:{id:item.id}})">
					<div>
						<div>{{$t('OrderNo')}}：{{item.id}}</div>
						<div>{{item.time}}</div>
					</div>
					<div>
						<div><span>{{$t('LenderAmount')}}</span><span>{{item.totalAmounts}} USDT</span></div>
						<div><span>{{$t('RemainingAmount')}}</span><span>{{item.delteAmounts}} USDT</span></div>
						<icon name='arrow' ></icon>
					</div>
					<div>
						<button class="bule" v-if='item.state == "1"' @click.stop="LenderRollbackUSDT(item.id)">{{$t('Withdraw')}}</button>
						<button class="gray" v-else-if='item.state == "3"'>已撤回</button>
						<button class="gray" v-else>已完成</button>
					</div>
				</div>
				<!-- <div class="MyLenderContractList">
					<div>
						<div>合同号：20200316001</div>
						<div>03-05 17:50</div>
					</div>
					<div>
						<div><span>放押金额</span><span>150 USDT</span></div>
						<div><span>剩余金额</span><span>0.00 USDT</span></div>
						<icon name='arrow' ></icon>
					</div>
					<div>
						<button class="gray">已完成</button>
					</div>
				</div> -->
			</div>
			<ul v-else class="lenderJoinList">
				<li><span>{{$t('OrderNo')}}</span><span>{{$t('Address')}}</span><span>{{$t('Progress')}}</span><span>{{$t('Quantity')}}/USDT</span></li>
				<li v-for="(item,index) in LenderJoinList" :key='index'>
					<div>
						<span :class="{active:item.id == activeId}">{{Number(item.id+1)}}</span>
					</div>
					<span>{{item.address.substr(0,4)+'****'+item.address.substr(-4,4)}}</span>
					<span>{{item.schedule}}%</span>
					<span>{{item.totalAmounts}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import { icon , Popup , Toast } from 'vant';
	export default{
		components:{ icon , Popup , Toast },
		data () {
			return {
				totalAmounts: 0,
				delteAmounts: 0,
				LenderList: [],
				active: 1,
				LenderJoinList: [],
				activeId: '',
				pledgeContractType: '',
			}
		},
		methods:{
			async init(){
				this.LenderList = [];
				this.LenderJoinList = [];
				
				//获取总量和剩余量
				var ids = await this.pledgeContractType.LenderListTotalInfo(this.$address).call();
				ids.ids.forEach(async item=>{
					var list = await this.pledgeContractType.LenderInfo(item).call();
					// console.log(list)
					if(list.state == '1'){
						this.totalAmounts += this.$fromWei(list.totalAmount,'','Mwei')
						this.delteAmounts += this.$fromWei(list.totalAmount,'','Mwei') - this.$fromWei(list.currentPaymented,'','Mwei')
					}
					this.LenderList.unshift({
						'totalAmounts': this.$fromWei(list.totalAmount,'','Mwei'),
						'delteAmounts': this.$fromWei(list.totalAmount,'','Mwei') - this.$fromWei(list.currentPaymented,'','Mwei'),
						'id': item,
						'time': this.$toTime(list.createTime,'Y-M-D H:i'),
						'state': list.state,
					})
					
					this.LenderList.sort(function(a,b){
						return b.id - a.id
					})
				})
				// console.log(this.LenderList)
				
				// 获取所有放押订单列表
				this.pledgeContractType.CurrentLenderIdAndTotal().call(async (err,result)=>{
					// console.log(result)
					this.activeId = result.index
					for (var i = (result.total-1);i >= 0;i--) {
						var list = await this.pledgeContractType.LenderInfo(i).call();
						
						if(list.state == '3') continue 
						
						this.LenderJoinList.unshift({
							'totalAmounts': this.$fromWei(list.totalAmount,'','Mwei'),
							'schedule': Math.floor(this.$fromWei(list.currentPaymented,'','Mwei') / this.$fromWei(list.totalAmount,'','Mwei') * 100),
							'id': i,
							'address': list.owner
						})
						
						this.LenderJoinList.sort(function(a,b){
							return a.id - b.id
						})
					}
				});
			},
			LenderRollbackUSDT(id){
				this.pledgeContractType.LenderRollbackUSDT(id).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			}
		},
		mounted() {
			if(localStorage.getItem('tokenType') == 'GABA'){
				this.pledgeContractType = this.$contract.pledgeGABA.methods
			}else{
				this.pledgeContractType = this.$contract.pledgeNXX.methods
			}
			this.init()
		}
	}
</script>

<style lang='scss' scoped>
	.box{
		padding: 20px 26px 0;
		
		.lenderTop{
			width: 100%;
			height: 314px;
			background-color: rgba($color: #2A78C8, $alpha: 0.61);
			border-radius: 10px;
			display: flex;
			flex-direction: column;
			padding: 0 16px;
			
			&>div:nth-child(1){
				width: 100%;
				height: 206px;
				font-size: 40px;
				display: flex;
				padding: 24px 0;
				border-bottom: 1px dotted #020C1E;
				
				&>div{
					width: 50%;
					height: 100%;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					
					&>div{
						font-size: 30px;
					}
				}
				
				&>div:nth-child(1){
					border-right: 1px dotted #020C1E;
				}
			}
			
			&>div:nth-child(2){
				width: 100%;
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;
				font-size: 40px;
				position: relative;
				
				&>img{					
					width: 114px;
					height: 96px;
					margin-top: 10px;
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
				}
				
				&>.van-icon{
					font-size: 25px;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
				}
				
			}
		}
	}
	
	.MyLenderContract{
		margin-top: 20px;
		
		&>.MyLenderContractTitle{
			display: flex;
			align-items: center;
			font-size: 32px;
			margin-bottom: 14px;
			justify-content: space-around;
			
			&>span{
				padding-bottom: 8px;
				border-bottom: 4px solid transparent;
			}
			
			.active{
				border-bottom: 4px solid #3A9CFF;
			}
		}
		
		&>.MyLenderContractListBox{
			.MyLenderContractList{
				width: 100%;
				height: 246px;
				background-color: rgba($color: #1C528A, $alpha: 0.67);
				border-radius: 10px;
				margin-bottom: 16px;
				display: flex;
				flex-direction: column;
				
				&>div:nth-child(1){
					display: flex;
					justify-content: space-between;
					align-items: center;
					color: #8E99A4;
					font-size: 24px;
					padding: 9px  18px 9px 26px;
				}
				
				&>div:nth-child(2){
					width: 100%;
					height: 100px;
					font-size: 28px;
					background-color: rgba($color: #011740, $alpha: 0.59);
					display: flex;
					flex-direction: column;
					justify-content: center;
					padding: 0 18px 0 26px;
					position: relative;
					
					&>div{
						text-align: left;
						
						&>span:nth-child(1){
							margin-right: 26px;
						}
					}
					
					.van-icon{
						font-size: 25px;
						position: absolute;
						right: 10px;
						top: 50%;
						transform: translateY(-50%);
					}
				}
				
				&>div:nth-child(3){
					flex: 1;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					
					button{
						width: 140px;
						height: 48px;
						border-radius: 8px;
						font-size: 24px;
						border: none;
					}
					
					.bule{
						background-color: #3A9CFF;
					}
					
					.gray{
						background-color: #537C95;
					}
					
				}
			}
		}
		
		&>.lenderJoinList{
			width: 100%;
			height: 646px;
			overflow-y: scroll;
			background-color: rgba($color: #011740, $alpha: .59);
			border-radius: 10px;
			display: flex;
			flex-direction: column;
			padding-top: 20px;
			
			&>li:nth-child(1){
				font-size: 28px;
			}
			
			&>li{
				font-size: 24px;
				display: flex;
				margin-bottom: 10px;
				
				&>span,&>div{
					flex: 1;
					
					&>span{
						position: relative;
					}
				}

				.active::after{
					content: '';
					position: absolute;
					width: 16px;
					height: 16px;
					background-color: #07F988;
					border-radius: 50%;
					top: 50%;
					transform: translateY(-50%);
					left: -30px;
				}
			}
			
		}
	}
</style>
