<template>
	<div class="box">
		<div class="fundReceive">
			<!-- <img class="fundNxx" src="../../assets/NXX.png" /> -->
			<img class="fundRecord" src="../../assets/fundRecord.png" @click="$router.push('/exchange/view/fundRecord')" />
			<div class="fundReceiveIncome">
				<div><img src="../../assets/fundNxx.png"/><div><span>{{nxx}}</span><span>NXX</span></div></div>
				<div><img src="../../assets/fundOnef.png"/><div><span>{{onef}}</span><span>ONEF</span></div></div>
			</div>
			<div class="IncomeDetails">
				<div>
					<span>{{profix.toFixed(2)}}</span>
					<span>{{$t('ReturnOnEquity')}}/NXX</span>
				</div>
				<div>
					<span>{{capital.toFixed(2)}}</span>
					<span>{{$t('WithdrawablePrincipal')}}/ONEF</span>
				</div>
			</div>
			<button @click="Settlement">{{$t('receive')}}</button>
		</div>
		<div class="equityTreasure">
			<p>{{$t('EquityTreasure')}}</p>
			<div class="equityTreasureSelect">
				<div v-for="(item,index) in selectList" :key='index' @click="selectFun(item.type)">
					<span>{{item.title}}</span>
					<div :class="{active : active == item.type}">{{item.Content}}</div>
				</div>
			</div>
			<div class="equityTreasureInput">
				<input type="number" v-model="value" :placeholder="$t('EnterAmount')" @input="equityTreasureInput" />
				<button @click="PaymentPlan">{{$t('Partake')}}</button>
			</div>
			<div class="Balance">
				{{$t('Available')}} {{onef}} ONEF
			</div>
			<div class="details" @click="nxxDetails=true">
				<img src="../../assets/details.png" />
				<span>{{$t('GettoKnowDetails')}}</span>
			</div>
		</div>
		
		<div class="Statistics">
			<div class="StatisticsBox">
				<div>
					<p>{{lockupPool}}</p>
					<span>{{$t('LockPool1')}}/ONEF</span>
				</div>
				<div>
					<p>{{dividendPool}}</p>
					<span>{{$t('BonusPool')}}/NXX</span>
				</div>
			</div>
		</div>
		
		<div class="exchangeRecord">
			<p>{{$t('ParticipationRecord')}}</p>
			<div class="exchangRecordContent">
				<div class="exchangRecordList" v-for="(item,index) in fundJoinList" :key="index">
					<div>
						<span v-if="item.day == '30'">{{$t('dayslocked1')}}</span>
						<span v-else-if="item.day == '180'">{{$t('dayslocked2')}}</span>
						<span v-else-if="item.day == '360'">{{$t('dayslocked3')}}</span>
						<span>{{$toTime(item.createTime,'M.D H:i')}}</span>
					</div>
					<div v-if="item.status" class="invalid">{{$t('Done')}}</div>
					<div v-else class="effective">{{$t('normal')}}</div>
					<div><span>-{{item.amount}} ONEF</span></div>
				</div>
				<!-- <div class="exchangRecordList">
					<div><span>{{$t('dayslocked2')}}</span><span>+150 NXX</span></div>
					<div class="invalid">{{$t('invalid')}}</div>
					<div><span>03.05 17:50</span><span>-150 ONEF</span></div>
				</div> -->
				<div class="noRecord" v-if="Record">
					<img src="../../assets/noRecord.png"/>
					<span>{{$t('NoTransaction')}}</span>
				</div>
			</div>
		</div>
		
		<!-- <popup v-model="authorizedAccess" >
			<div class="authorizedAccessBox">
				<div class="authorizedAccessTop">
					<div><img src="../../assets/yellowRing.png"/></div>
					<p>合约额度授权</p>
					<span>{{authorizedAccessTime}}</span>
				</div>
				<div class="authorizedAccessText">合约额度授权指合约对资产可操控的授信额度，<br/>与真实可用额度无关，推荐使用多少授权多少。</div>
				<div class="authorizedAccessInput">
					<p>该交易需要授权额度: {{value}} ONEF</p>
					<div>
						<input type="number" v-model="authorizedAccessNumber" />
						<div>ONEF</div>
					</div>
					<span>可用授权额度 {{authorizedAccessAmount}} ONEF</span>
				</div>
				<div class="authorizedAccessButton" @click="onefAuthorization">{{$t('sure')}}</div>
			</div>
		</popup> -->
		
		<authorization
		:show="authorizedAccess" 
		:click="onefAuthorization" 
		:needAuthorization='value' 
		:availableAuthorizations="authorizedAccessAmount" 
		:tokenType="'ONEF'"
		@authorizedAccessInput="getNumber"></authorization>
		
		<popup v-model="nxxDetails" >
			<div class="nxxDetails">
				<p>{{$t('LockedPosition')}}</p>			                            
				<p>{{$t('FundDetailsText1')}}</p>
				<p>{{$t('FundDetailsText11')}}</p>
				<p>{{$t('FundDetailsText10')}}</p>
				<div><span>1</span><span>{{$t('FundDetailsText2')}}</span></div>
				<div><span>2</span><span>{{$t('FundDetailsText3')}}</span></div>
				<div><span>3</span><span>{{$t('FundDetailsText4')}}</span></div>
				<div><span>4</span><span>{{$t('FundDetailsText5')}}</span></div>
				<p>{{$t('FundDetailsText6')}}</p>
				<p>{{$t('FundDetailsText7')}}</p>
				<p>{{$t('FundDetailsText8')}}</p>
				<p>{{$t('FundDetailsText9')}}</p>
				<img @click="nxxDetails=false" src="../../assets/out.png" />
			</div>
