<script>
	import { Popup , Toast } from 'vant';
	import axios from 'axios';
	import Authorization from '../../components/Authorization.vue';
	
	export default {
		components: { Popup , Toast , Authorization },
		data(){
			return{
				Record: false,
				nxxDetails: false,
				authorizedAccess: false,
				invite: false,
				selectList: [],
				active: 0,
				onef: 0,
				value: '',
				lockupPool: 0,
				dividendPool: 0,
				nxx: 0,
				profix: 0,
				capital: 0,
				fundJoinList: [],
				authorizedAccessAmount: 0,
				authorizedAccessNumber: '',
			}
		},
		methods:{
			async init(){
				this.authorizedAccess = false;
				
				this.onef = this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$address).call())
				this.dividendPool = this.$fromWei(await this.$contract.nxxToken.methods.balanceOf(this.$contract.financialPlan._address).call(),0)
				// console.log(this.dividendPool)
				this.income = await this.$contract.financialPlan.methods.WithdrawDepositProfixOrCapital().call({from:this.$address})
				this.profix = this.$fromWei(this.income.profix);
				this.capital = this.$fromWei(this.income.capital);
				this.lockupPool = this.$fromWei(await this.$contract.onefToken.methods.balanceOf(this.$contract.financialPlan._address).call());
				this.nxx = this.$fromWei(await this.$contract.nxxToken.methods.balanceOf(this.$address).call())
				
				// ONEF是否授权
				var authorizedAccess = await this.$contract.onefToken.methods.allowance(this.$address,this.$contract.financialPlan._address).call({from:this.$address})
				this.authorizedAccessAmount = this.$fromWei(authorizedAccess)
				
				//是否绑定关系
				var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
				this.invite = parseInt(invite,16) ? false : true ;
				
				// 参与记录
				this.$contract.financialPlan.methods.PaymentedPlanDetailAtIndex(this.$address,0).call(async (err,totalCount)=>{
					if(!totalCount) return
					var typeId = await this.$contract.financialPlan.methods.CurrentPlansList().call();
					
					if(Number(totalCount.totalCount)>0){
						this.fundJoinList = [];
						for (var i = 0; i < totalCount.totalCount; i++) {	
							
							this.$contract.financialPlan.methods.PaymentedPlanDetailAtIndex(this.$address,i).call(async (err,result)=>{
								
								this.fundJoinList.unshift({
									amount: this.$fromWei(result.amount),
									createTime: result.createTime,
									nextWithdrawTime: result.nextWithdrawTime,
									status: result.isSuccessed,
									day: typeId.lockedDays[result.planID]
								})
								
								this.fundJoinList.sort(function(a,b){
									return b.createTime - a.createTime
								})
							
							})
							
						}
						// console.log(this.fundJoinList)
					}else{
						this.Record = true
					}
				})
				// var totalCount = Number((await this.$contract.financialPlan.methods.PaymentedPlanDetailAtIndex(this.$address,0).call()).totalCount);
			},
			selectFun(key){
				this.active = key
			},
			PaymentPlan(){
				var value = Number(this.value);
				if( value < 100 || value % 10 != 0 ){
					return Toast.fail(this.$t('tips2'))
				}else if(this.invite){
					return Toast.fail(this.$t('tips3'))
				}else if(this.authorizedAccessAmount < value){
					return this.authorizedAccess = true; 
				}
				// console.log(this.active,this.$toWei(this.value))
				this.$contract.financialPlan.methods.PaymentPlan(this.active,this.$toWei(this.value.toString())).send({
					from: this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , console.error)
				.on('receipt' , result=>{
					console.log(result)
					this.init()
					this.value = ''
				})
			},
			equityTreasureInput(){
				var value = Number(this.value);
				if(value > this.onef){
					this.value = this.onef
				}
			},
			onefAuthorization(){
				this.$contract.onefToken.methods.approve(this.$contract.financialPlan._address,this.$web3.utils.toWei(this.authorizedAccessNumber.toString(),'ether')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			Settlement(){
				this.$contract.financialPlan.methods.WithdrawDepositProfixOrCapital().send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			getNumber(value){
				this.authorizedAccessNumber = value;
			}
		},
		mounted() {
			this.init()
			this.selectList = [
				{
					title: this.$t('daysofequity1'),
					Content: this.$t('monthly1'),
					type: 0
				},
				{
					title: this.$t('daysofequity2'),
					Content: this.$t('monthly2'),
					type: 1
				},
				{
					title: this.$t('daysofequity3'),
					Content: this.$t('monthly5'),
					type: 2
				}
			]
			
			
		}
	}
</script>

