<template>
	<div class="box van-tabbar-page">
		<div class="incomeTop">
			<div>
				<div>
					<img src="../../assets/gaba.png" />
					<span>{{gaba}} GABA</span>
				</div>
				<img @click="showQrcode" src="../../assets/qcode.png" />
			</div>
			<div>
				<div>
					<img src="../../assets/MyCrowdFunding.png" />
					<span>{{$t('MyParticipation')}}&nbsp;{{MyCrowdFunding}}&nbsp;GABA</span>
				</div>
			</div>
			<div>
				<div>
					<img src="../../assets/wallet.png" />
					<span>{{($address).substr(0,4)}}******{{($address).substr(-4,4)}}</span>
				</div>
				<span 
				v-clipboard:copy="$address"
				v-clipboard:success="onCopySuccess"
				v-clipboard:error="onCopyError">{{$t('Copy')}}</span>
			</div>
		</div>
		
		<div class="incomeContent">
			<ul>
				<li v-for="(item,index) in selectList" :key="index" :class="{ListActive:index == active}" @click="active = index">{{item}}</li>
			</ul>
			<div class="incomeListBox">
				<div class="incomeListBoxLeft" v-if="!active">
					<div v-for="(item,index) in list" :key="index" >
						<div class="incomeListTop">
							<div>
								<div><span>{{$t('No')}}{{Number(item.index)+1}}{{$t('Period')}}</span><span>{{item.latestDepositedTime}}</span></div>
								<rate v-model.number="item.lv" :size="14" readonly />
							</div>
							<div>
								<div>
									<div>
										<span>{{$t('CurrentState')}}:</span>
										<span v-if="item.statuse == '1'">{{$t('WaitingStart')}}</span>
										<span v-if="item.statuse == '2'" class="yellow">{{$t('Crowdfunding')}}</span>
										<span v-if="item.statuse == '3'">{{$t('Waiting')}}</span>
										<span v-if="item.statuse == '4'" class="red">{{$t('CrowdfundingFails')}}</span>
										<span v-if="item.statuse == '5' && Number(item.totalStProfit) > 0" class="green">{{$t('over1')}}</span>
										<span v-if="item.statuse == '5' && Number(item.totalStProfit) <= 0">{{$t('Waiting')}}</span>
										<span v-if="item.statuse == '6'" class="red">{{$t('CrowdfundingFails')}}</span>
									</div>
									<div v-if="item.statuse != '4' && item.statuse != '5' && item.statuse != '6' && item.statuse != '1'"><span>{{$t('Automaticity')}}:</span><income-switch v-model="item.autoReDepostied" @input='onInput(item.index,index)' size="18px" active-color="#8AFFA1" /></div>
								</div>
								<div>
									<span>{{item.totalAmount}}</span>
									<span>GABA</span>
								</div>
							</div>
						</div>
						<div class="incomeListCenter">
							<div><span>{{item.totalStProfit.toFixed(2)}}</span><span>{{$t('Earnings')}}</span></div>
							<div><span>{{item.totalDyProfit}}</span><span>{{$t('ReferralReward1')}}</span></div>
							<div><span>{{item.totalSumProfit}}</span><span>{{$t('Summation')}}</span></div>
						</div>
						<div class="incomeListBottom">
							<div v-if="item.statuse == '0'">{{$t('UnknownStatus')}}</div>
							<div v-else-if="item.autoReDepostied && iid >= Number(item.index)+4 && Number(item.totalStProfit) > 0 && Number(item.redepositedToOID)!= 1">{{$t('Has')}}<span>{{item.totalStProfit1}}GABA</span>{{$t('ReCast')}}{{Number(item.redepositedToOID)+1}}{{$t('Period')}}</div>
							<div v-else-if="item.statuse == '1'">{{$t('WaitingStart')}}</div>
							<div v-else-if="item.statuse == '2'">{{$t('Crowdfunding')}}</div>
							<div v-else-if="item.statuse == '3'">{{$t('Waiting')}}</div>
							<!-- <div v-if="item.statuse == '4'">{{$t('Failure')}}</div> -->
							<button v-else-if="item.statuse == '4' && Number(item.totalStProfit) > 0" disabled="disabled">{{$t('ReceiveReturn')}}</button>
							<button v-else-if="item.statuse == '4' && Number(item.totalStProfit) == 0" @click="Settlement(item.index)">{{$t('ReceiveReturn')}}</button>
							<button v-else-if="item.statuse == '5' && Number(item.totalStProfit) > 0" disabled="disabled">{{$t('over1')}}</button>
							<button v-else-if="item.statuse == '5' && Number(item.totalStProfit) == 0" @click="Settlement(item.index)">{{$t('Settlement')}}</button>
							<button v-else-if="item.statuse == '6' && Number(item.totalStProfit) > 0" disabled="disabled">{{$t('RiskSharing')}}</button>
							<button v-else-if="item.statuse == '6' && Number(item.totalStProfit) == 0" @click="Settlement(item.index)">{{$t('RiskSharing')}}</button>
							<!-- <div v-if="item.statuse == '6'">{{$t('RiskSharing')}}</div> -->
							<!-- <button>领取收益</button> -->
						</div>
					</div>
				</div>
				<div class="incomeListBoxRight" v-else>
					<div>
						<div>
							<img src="../../assets/income01.png" />
							<span>{{$t('MyInvitation')}}</span>
							<div>{{RecommendList}} &nbsp; {{$t('people')}}</div>
						</div>
						<div>
							<img src="../../assets/income02.png" />
							<span>{{$t('MyCommunity')}}</span>
							<div>{{TeamMemberTotal}} &nbsp; {{$t('people')}}</div>
						</div>
						<div>
							<img src="../../assets/income03.png" />
							<span>{{$t('CumulativeSharedRevenue')}}</span>
							<div>{{TotalDyAmountSum}} &nbsp; GABA</div>
						</div>
						<div>
							<img src="../../assets/income04.png" />
							<span>{{$t('CanReceiveSharedRevenue')}}</span>
							<div>{{DyProfit1}} &nbsp; GABA</div>
						</div>
					</div>
					
					<button @click="PoolWithdraw">{{$t('receive')}}</button>
				</div>
			</div>
		</div>
		
		
		<popup v-model="show" >
			<div class="drawIncomeBox">
				<div class="drawIncomeTop">
					<div>
						<p>0.00</p>
						<span>GABA</span>
					</div>
					<div>
						<div><span>{{$t('Callable')}}:</span><span>0.00 GABA</span></div>
						<div><span>{{$t('Destroy')}}:</span><span>0.00 GABA</span></div>
					</div>
				</div>
				<div class="drawIncomeButton">
					{{$t('receive')}}
				</div>
			</div>
		</popup>
		
		<popup v-model="qrcodeShow" >
			<div id="qrcode" ref="qrcode"></div>
		</popup>
		
	</div>
</template>

<script>
import { Switch as IncomeSwitch , Rate , Popup , Toast } from 'vant';
import QRCode from 'qrcodejs2'

export default {
	name: "income",
	components: { IncomeSwitch , Rate , Popup },
	data() {
		return {
			selectList: ["我的众筹","分享收益"],
			active: 0,
			checked: true,
			value: 5,
			show: false,
			list: [],
			gaba: 0,
			DyProfit: 0,
			DyProfit1: 0,
			RecommendList: 0,
			TeamMemberTotal: 0,
			TotalDyAmountSum: 0,
			qrcodeShow: false,
			qrcode: '',
			qrcodeCreateStatus: false,
			MyCrowdFunding: 0,
			iid: 0,
		}
	},
  methods: {
    init(){
		
		// 获取token余额
		this.$contract.gabaToken.methods.balanceOf(this.$address).call((err, res) => {
		    if (!err) {
		        this.gaba = this.$fromWei(res)
		    }
		})
		
		// 获取动态收益
		this.$contract.mainContract.methods.PoolBalanceOf(this.$address).call((err, res) => {
		    if (!err) {
		        this.DyProfit = this.$fromWei(res)
				this.DyProfit1 = this.$fromWei(res,2)
		    }
		})
		
		// 累计动态收益
		this.$contract.mainContract.methods.TotalDyAmountSum(this.$address).call((err, res) => {
		    if (!err) {
		        this.TotalDyAmountSum = this.$fromWei(res,2)
		    }
		})
		
		// 当前最新轮次id
		this.$contract.mainContract.methods.RoundStatusAt(65535).call((err, res) => {
		    if (!err) {
		       this.iid = Number(res.iid)
		    }
		})
		
		this.$contract.mainContract.methods.DepositedRoundOIDS(this.$address).call({from:this.$address},(err,result)=>{
			this.list = [];
			result.ids.forEach(async (item)=>{
				var list = await this.$contract.mainContract.methods.DepositedInfo(this.$address,item).call();
				// console.log(list)
				// var totalStProfit1 = await this.$contract.mainContract.methods.DoSettlement(item).call({from:this.$address});
				var totalDyProfit = (this.$fromWei(list.totalDyProfit)+this.$fromWei(list.totalMrgProfit)).toFixed(2);
				var totalSumProfit = Number(list.totalStProfit)+Number(list.totalDyProfit)+Number(list.totalMrgProfit);
				var totalSumProfit = (this.$fromWei(list.totalStProfit)+this.$fromWei(list.totalDyProfit)+this.$fromWei(list.totalMrgProfit)).toFixed(2);
				var totalStProfit1 = this.$fromWei(list.totalStProfit).toString().split('.')
				if(totalStProfit1[1] != undefined){
					totalStProfit1 = totalStProfit1[0]+'.'+totalStProfit1[1].substr(0,1)
				}else{
					totalStProfit1 = totalStProfit1[0]
				}
				var box = {
					statuse : list.statuse,
					autoReDepostied : list.autoReDepostied,
					latestDepositedTime : this.$toTime(list.latestDepositedTime,'Y-M-D'),
					lv : Number(list.lv),
					totalAmount : this.$fromWei(list.totalAmount),
					totalDyProfit : totalDyProfit,
					totalStProfit : this.$fromWei(list.totalStProfit),
					totalStProfit1 : totalStProfit1,
					totalSumProfit : totalSumProfit,
					index : item,
					redepositedToOID : list.redepositedToOID
				};
				this.list.push(box)
				this.list.sort(function(a,b){
					return a.index - b.index
				})
			})
			// console.log(this.list)
		})
		
	},
	getData(){
		this.selectList = [this.$t('MyCrowdFunding'),this.$t('ReferralReward1')]
		
		//我的邀请
		this.$contract.recommend.methods.RecommendList(this.$address, 0).call((err, res) => {
			if (!err) {
			    this.RecommendList = res.len
			}
		})
		
		//我的团队
		this.$contract.recommend.methods.TeamMemberTotal(this.$address).call((err, res) => {
			if (!err) {
			     this.TeamMemberTotal = res
			}
		})
		
		//我的参与
		this.$contract.mainContract.methods.CurrentDepositedTotalCount(this.$address).call((err, res) => {
			if (!err) {
			    this.MyCrowdFunding = this.$fromWei(res)
			}
		})
	},
	Settlement(index){
		//结算
		this.$contract.mainContract.methods.DoSettlement(index).send({
			from: this.$address,
		}).on('transactionHash' , txHash =>{
			console.log(txHash)
		}).on('error' , console.error)
		.on('receipt' , result=>{
			console.log(result)
			this.init()
		})
	},
	onInput(id,index){
		if(this.list[index].autoReDepostied == false)return this.list[index].autoReDepostied = true
		this.list[index].autoReDepostied = false;
		this.$contract.mainContract.methods.EnableAutoRedepostied(id).send({
		    from: this.$address,
		}).on('transactionHash' , txHash =>{
		    console.log(txHash)
		}).on('error' , console.error)
		.on('receipt' , result=>{
		    console.log(result)
			this.list[index].autoReDepostied = true;
			this.init()
		})
	},
	onCopySuccess() {
		Toast.success(this.$t('CopySuccess'))
	},
	onCopyError() {
		Toast.fail(this.$t('CopyFail'))
	},
	PoolWithdraw(){
		if(this.DyProfit <= 0){return}
		var DyProfit = this.$toWei(this.DyProfit.toString())
		this.$contract.mainContract.methods.PoolWithdraw(DyProfit).send({
		    from: this.$address,
		}).on('transactionHash' , txHash =>{
		    console.log(txHash)
		}).on('error' , console.error)
		.on('receipt' , result=>{
		    console.log(result)
			this.init()
		})
	},
	crateQrcode(){
		// this.qrcodeText = 'www.baidu.com'
		this.qrcode = new QRCode('qrcode',{
		    width: 200, // 设置宽度，单位像素
		    height: 200, // 设置高度，单位像素
		    text: this.$address   // 设置二维码内容或跳转地址
		})
	},
	showQrcode(){
		this.qrcodeShow = true;
		
		if(!this.qrcodeCreateStatus){			
			this.$nextTick(() => {
				this.qrcodeCreateStatus = true;
				this.crateQrcode()
			})
		}
	},
  },
  mounted() {
		this.init()
		this.getData()
  }
}
</script>

