<template>
	<div class="van-tabbar-page box">
		<div class="Detection">
			<p>{{$t('DetectionOfAuthorizedQuota')}}</p>
			<div>
				<span>{{$t('AvailableBalance')}} {{usdt}} USDT</span>
				<span>{{$t('AuthorizedQuota')}} {{authorizedAccess}} USDT</span>
			</div>
			<div>
				<input type="number" :placeholder="$t('AcquireTheAuthorizedQuota')" v-model="authorizedAmount" />
				<button @click="usdtAuthorization">{{$t('sure')}}</button>
			</div>
			<!-- <div>
				<img src="../../../assets/details.png" />
				授权额度
			</div> -->
		</div>
		<div class="lender">
			<p>{{$t('IPledge')}}</p>
			<div>
				<span v-if="pldegeType == 'GABA'">{{changeProps}} USDT = 1 GABA</span>
				<span v-else>{{changeProps}} USDT = 1 NXX</span>
			</div>
			<div>
				<input type="number" :placeholder="$t('InputAmount')" v-model="usdtAmount" />
				<div>
					<img />
					USDT
				</div>
			</div>
			<div>{{$t('Available')}}  {{usdt}}  USDT</div>
			<div class="footer">
			    <checkbox class="checkbox" v-model="checked">
			        {{$t('IHaveRead')}}<span @click.stop='rule = true' style="color: #4D95FC;">《{{$t('lenderText01')}}》</span>，{{$t('involved')}}
			    </checkbox>
			    <button :disabled="!checked" @click="lenderUSDT">{{$t('NextStep')}}</button>
			</div>
		</div>
		
		<popup v-model="rule">
			<div class="rule">
				{{$t('lenderText01')}}<br />
				{{$t('lenderText02')}}<br />
				1.{{$t('lenderText03')}}<br />
				2.{{$t('lenderText04')}}<br />
				3.{{$t('lenderText05')}}<br />
				4.{{$t('lenderText06')}}<br />
				5.{{$t('lenderText07')}}<br />
			</div>
		</popup>
	</div>
</template>

<script>
	import { Popup , Toast , Checkbox } from 'vant';
	// import NXXToken from '../../../static/NXXToken.json';
	
	export default{
		components:{ Popup , Toast , Checkbox },
		data () {
			return {
				pledgeContractType: '',
				checked: false,
				authorizedAmount: '',
				availableAuthorizedAccess: 0.00,
				authorizedAccess: 0.00,
				usdtAmount: '',
				usdt: 0,
				lenderUSDTMinLimit: 0,
				lenderUSDTBaseLimit: 0,
				invite: false,
				changeProps: 0,
				pldegeType: '',
				rule: false,
			}
		},
		methods:{
			async init(){
				this.authorizedAmount = '';
				this.usdtAmount = '';
				
				var PledgeItemList = await this.pledgeContractType.methods.PledgeItemList().call()
				this.changeProps = this.$fromWei(PledgeItemList.changeProps[0],'','Mwei');
				
				// USDT是否授权
				this.authorizedAccess = this.$fromWei(await this.$contract.usdtToken.methods.allowance(this.$address,this.pledgeContractType._address).call({from:this.$address}),'','Mwei')
				
				//USDT
				this.usdt = this.$fromWei(await this.$contract.usdtToken.methods.balanceOf(this.$address).call(),'','Mwei')
				
				//放押限制
				this.lenderUSDTMinLimit = this.$fromWei(await this.pledgeContractType.methods.lenderUSDTMinLimit().call(),'','Mwei');
				this.lenderUSDTBaseLimit = this.$fromWei(await this.pledgeContractType.methods.lenderUSDTBaseLimit().call(),'','Mwei');
				
				// 是否绑定关系
				var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
				this.invite = parseInt(invite,16) ? false : true ;
			},
			usdtAuthorization(){
				this.$contract.usdtToken.methods.approve(this.pledgeContractType._address,this.$web3.utils.toWei(this.authorizedAmount.toString(),'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			},
			lenderUSDT(){
				if(Number(this.usdtAmount) > Number(this.authorizedAccess)){
					return Toast.fail(this.$t('PledgeWarningAlertText01'))
				}else if(Number(this.usdtAmount) > Number(this.usdt)){
					return Toast.fail(this.$t('InsufficientBalance'))
				}else if(Number(this.usdtAmount) < this.lenderUSDTMinLimit || Number(this.usdtAmount)%this.lenderUSDTBaseLimit != 0){
					return Toast.fail(this.$t('least')+this.lenderUSDTMinLimit+this.$t('And')+this.lenderUSDTBaseLimit+this.$t('InMultiplesOf'))
				}else if(this.invite){
					return Toast.fail(this.$t('tips3'))
				}
				this.pledgeContractType.methods.LendingUSDT(this.$web3.utils.toWei(this.usdtAmount.toString(),'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' , result=>{
					this.init()
				})
			}
		},
		mounted() {
			if(localStorage.getItem('tokenType') == 'GABA'){
				this.pledgeContractType = this.$contract.pledgeGABA
				this.pldegeType = 'GABA'
			}else{
				this.pledgeContractType = this.$contract.pledgeNXX
				this.pldegeType = 'NXX'
			}
			this.init()
		}
	}
</script>

