			}
			
			&>div:nth-child(4){
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 26px;
				color: #8E99A4;
				margin-top: 24px;
				
				&>img{
					width: 36px;
					height: 36px;
					margin-right: 10px;
				}
			}
		}
		
		.lender{
			width: 100%;
			height: 616px;
			background-color: rgba($color: #2A78C8, $alpha: 0.61);
			border-radius: 10px;
			padding: 42px 28px 0;
			display: flex;
			flex-direction: column;
			margin-top: 32px;
			
			&>p{
				font-size: 44px;
			}
			
			&>div:nth-child(2){
				display: flex;
				flex-direction: column;
				font-size: 32px;
				color: #F5F5F5;
				text-align: left;
				margin: 34px 0 18px;
			}
			
			&>div:nth-child(3){
				width: 100%;
				display: flex;
				font-size: 36px;
				justify-content: space-between;
				border: #3A9CFF solid 2px;
