const numFixed = (num , digit) => {
	var num = num.toString();
	var index = num.indexOf('.');
	if(index !== -1){
		num = num.substring(0, digit + index + 1)
	}else{
		num = num.substring(0)
	}
	return parseFloat(num)
}

const upFixed = (num, fix) => {
	// num为原数字，fix是保留的小数位数
	let result = '0'
	if (Number(num) && fix > 0) { // 简单的做个判断
		fix = +fix || 2
		num = num + ''
		if (/e/.test(num)) { // 如果是包含e字符的数字直接返回
			result = num
		} else if (!/\./.test(num)) { // 如果没有小数点
			result = num + `.${Array(fix + 1).join('0')}`
		} else { // 如果有小数点
			num = num + `${Array(fix + 1).join('0')}`
			let reg = new RegExp(`-?\\d*\\.\\d{0,${fix}}`)
			let floorStr = reg.exec(num)[0]
			if (+floorStr >= +num) {
				result = floorStr
			} else {
				let floorNumber = +floorStr + +`0.${Array(fix).join('0')}1`
				let point = /\./.test(floorNumber + '') ? '' : '.'
				let floorStr2 = floorNumber + point + `${Array(fix + 1).join('0')}`
				result = reg.exec(floorStr2)[0]
			}
		}
	}
	return result
}

const toWei = (balance) => {
    return $web3.utils.toWei(balance, 'ether')
}

const toTime = (times , type) => {
	function add(m){return m<10?'0'+m:m }
	var time = new Date(times * 1000);
	var y = time.getFullYear();
	var m = time.getMonth()+1;
	var d = time.getDate();
	var h = time.getHours();
	var mm = time.getMinutes();

	if (type === 'Y-M-D') {
		return y+'.'+add(m)+'.'+add(d);
	} else if (type === 'Y-M-D H:i') {
		return y+'-'+add(m)+'-'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'M.D H:i'){
		return add(m)+'.'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'Y/M/D H:i'){
		return y+'/'+add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'YMD'){
		return y+add(m)+add(d);
	}
}

export { $web3, contract, fromWei, toWei , toTime , guide , upFixed , numFixed }
