<template>
	<div class="box">
		<div class="exchangeRecord">
			<p>{{$t('RevenueRecord')}}</p>
			<div class="exchangRecordContent">
				<div class="exchangRecordList" v-for="(item,index) in fundRecord" :key="index">
					<div><span>{{$t('Types')}}</span><!-- <span>{{$t('Time')}}</span> --><span>{{$t('income')}}</span></div>
					<div>						
						<span>{{$t('PledgeProfit')}}</span>
						<span v-if="item.time">{{$toTime(item.time,'Y-M-D H:i')}}</span>
						<span>+{{item.Amount}} USDT</span>
					</div>
				</div>
				<div class="noRecord" v-if="Record">
					<img src="../../assets/noRecord.png"/>
					<span>{{$t('NoTransaction')}}</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		data(){
			return {
				Record: false,
				fundRecord: [],
				pledgeContractType: '',
			}
		},
		mounted() {
			if(localStorage.getItem('tokenType') == 'GABA'){
				this.pledgeContractType = this.$contract.pledgeGABA
				// this.topic0 = '0x387ff6b7984c898994e2278a86b9f5aee59b9bd15045984f0bfe70e05d6c991d'
			}else{
				this.pledgeContractType = this.$contract.pledgeNXX
				// this.topic0 = '0x387ff6b7984c898994e2278a86b9f5aee59b9bd15045984f0bfe70e05d6c991d'
			}
			//0x387ff6b7984c898994e2278a86b9f5aee59b9bd15045984f0bfe70e05d6c991d
			axios.get('https://api-cn.etherscan.com/api',{
				params: {
					"module":'logs',
					"action":'getLogs',
					"fromBlock":0,
					"toBlock":'latest',
					"address":this.pledgeContractType._address,
					"topic0":'0x387ff6b7984c898994e2278a86b9f5aee59b9bd15045984f0bfe70e05d6c991d',
					"topic1": this.$web3.eth.abi.encodeParameter('address',this.$address),
					// "topic3": this.$web3.eth.abi.encodeParameter('uint16','100'),
					"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU',
				}
			}).then( result => {
				// console.log(result)
				if(result.data.result.length == 0){
					this.Record = true
				}
				result.data.result.forEach( item => {
					if(item.topics[3]){
						var time = this.$web3.eth.abi.decodeParameters(['uint256'],item.topics[3])[0]
					}else{
						var time = false;
					}
					this.fundRecord.unshift({
						address: this.$web3.eth.abi.decodeParameters(['address'],item.topics[1])[0],
						Amount: this.$fromWei(this.$web3.eth.abi.decodeParameters(['uint256'],item.topics[2])[0],'','Mwei'),
						time: time
					})
				})
				// console.log(this.fundRecord)
			}).catch( err => {
				console.log(err)
			});
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		width: 100vw;
		height: 100vh;
		padding: 26px 28px 0;
		
		.exchangeRecord{
			width: 100%;
			height: 1082px;
			background-color: rgba($color: #4A7EB5, $alpha: 0.39);
			border-radius: 10px;
			font-size: 28px;
			padding: 0 35px;
			display: flex;
			flex-direction: column;
			position: relative;
			
			&>p{
				padding: 10px 0 8px;
			}
			
			&>.exchangRecordContent{
				flex: 1;
				
				&>.exchangRecordList{
					font-size: 24px;
					margin-bottom: 10px;
					
					&>div{
						width: 100%;
						display: flex;
						align-items: center;
						
						&>span{
							flex: 1;
						}
						
						// &>span:nth-child(2){
						// 	flex: 1;
						// }
					}
				}
				
				&>.noRecord{
					font-size: 24px;
					color: #758D9C;
					display: flex;
					flex-direction: column;
					align-items: center;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
					
					&>img{
						width: 67px;
						height: 85px;
					}
				}
			}
		}
		
	}
</style>
