<template>
	<div class="van-tabbar-page box">
		<img src="../../assets/My-Logo.png" />
		<div class="myCode">
			<div>
				<span>{{$t('InvitationCode')}}</span>
				<p>{{invitationCode}}</p>
			</div>
			<img src="../../assets/gaba.png" />
			<self-icon type="copy"  
			v-clipboard:copy="invitationCode"
			v-clipboard:success="onCopySuccess"
			v-clipboard:error="onCopyError">
			</self-icon>
		</div>
		
		<div class="myContent">
			<div class="myContentList">
				<div>
					<self-icon type="href"></self-icon>
					<span>{{$t('InvitationLink')}}</span>
				</div>
				<div
				 v-clipboard:copy="invitationLink"
				 v-clipboard:success="onCopySuccess"
				 v-clipboard:error="onCopyError"
				>{{$t('Copy')}}</div>
			</div>
			<div class="myContentList" @click="MyLnvitationList()">
				<div>
					<self-icon type="inviteList"></self-icon>
					<span>{{$t('InvitationList')}}</span>
				</div>
				<div>
					<self-icon type="Right"></self-icon>
				</div>
			</div>
			<div class="myContentList" @click="show = true">
				<div>
					<self-icon type="language"></self-icon>
					<span>{{$t('Language')}}</span>
				</div>
				<div>
					<self-icon type="Right"></self-icon>
				</div>
			</div>
			<div class="myContentList" @click="About()">
				<div>
					<self-icon type="about"></self-icon>
					<span>{{$t('About')}}</span>
				</div>
				<div>
					<self-icon type="Right"></self-icon>
				</div>
			</div>
			<div class="myContentList" @click="getUserStatus">
				<div>
					<self-icon type="userStatus"></self-icon>
					<span>{{$t('userStatus')}}</span>
				</div>
				<div>
					<self-icon type="Right"></self-icon>
				</div>
			</div>
		</div>
		

		<popup v-model="show"  position="bottom">
			<div class="languageSelectTitle">
				<span>{{$t('SelectLanguage')}}</span>
			</div>
			<div class="languageSelect" v-for="(item,index) in languageMessages" :key="index" @click="changeLanguage(item.type)">
				<img :src='"../../assets/"+item.type+".png"' />
				<span>{{item.title}}</span>
				<self-icon type="hook" class="hook" v-show="item.type == $i18n.locale"></self-icon>
			</div>
		</popup>
		
		<popup v-model="userStatusShow" class="userProp">
			<div class="userStatusBox">
				<img src='../../assets/out.png' @click="userStatusShow = false" />
				<div class="userStatusTitle">
					{{$t('userStatus')}}
				</div>
				<div class="userStatusContent">
					<span>{{$t('state')}}</span>
					<span>{{userStatusBull ? $t('effective') : $t('invalid')}}</span>
				</div>
				<div class="userStatusContent">
					<span>{{$t('EffectiveTime')}}</span>
					<span>{{userStatusBull ? $toTime(userStatusTime,'Y-M-D') : ""}}</span>
				</div>
			</div>
		</popup>
	</div>
</template>

<script>
import { Toast , Popup } from 'vant'

export default {
	name: 'my',
	components: { Popup },
	data() {
		return {
			grade: '1',
			invitationCode: '******',
			invitationLink: '',
			myInviter: '******',
			showInviter: false,
			path: '/my/view/JoinNode',
			show: false,
			languageMessages: [
				{
					'type': 'zh',
					'title': '简体中文',
				},
				{
					'type': 'en',
					'title': 'English',
				},
			],
			userStatusShow: false,
			userStatusBull: false,
			userStatusTime: 0,
		}
	},
	methods: {
		onCopySuccess() {
			Toast.success(this.$t('CopySuccess'))
		},
		onCopyError() {
			Toast.fail(this.$t('CopyFail'))
		},
		MyLnvitationList(){
			this.$router.push('/my/view/myLnvitationList')
		},
		About(){
			this.$router.push('/my/view/About')
		},
		// onSelect(item) {
		// 	// 默认情况下，点击选项时不会自动关闭菜单
		// 	// 可以通过 close-on-click-action 属性开启自动关闭
		// 	this.show = false;
		// 	Toast(item.name);
		// },
		changeLanguage(type){
			localStorage.setItem('locale',type)
			this.$i18n.locale = type;
			this.show = false;
		},
		async getData(){
			var IsValidMemberEx = await this.$contract.recommend.methods.IsValidMemberEx(this.$address).call();
			this.userStatusBull = IsValidMemberEx[0];
			this.userStatusTime = IsValidMemberEx[1];
			
			var invite = await this.$contract.recommend.methods.AddressToShortCode(this.$address).call();
			if(!parseInt(invite,16))return
			this.invitationCode = this.$web3.utils.hexToString(invite)
			this.invitationLink = window.location.href.substr(0,window.location.href.length-3)+'?code='+this.invitationCode
			console.log(this.invitationLink)
		},
		getUserStatus(){
			this.userStatusShow = true;
		}
	},
	mounted() {
		this.getData()
	}
}
</script>

<style lang="scss" scoped>
	.box{
		padding: 20px 0 0;
		
		&>img{
			width: 306px;
		}
		
		.myCode{
			width:724px;
			height:192px;
			display: flex;
			align-items: center;
			justify-content: center;
			background-image: url(../../assets/myCodeBg.png);
			background-size: 100%;
			margin: 20px auto 0;
			position: relative;
			
			&>div{
				display: flex;
				flex-direction: column;
				
				&>span{
					font-size: 32px;
				}
				
				&>p{
					font-size: 52px;
				}
			}
			
			&>img{
				width: 65px;
				position: absolute;
				left: 80px;
				top: 50%;
				transform: translateY(-50%);
			}
			
			.van-iconfont{
				margin-top: 12px;
				position: absolute;
				right: 50px;
				top: 50%;
				transform: translateY(-50%);
			}
		}
		
		.myContent{
			margin-top: 50px;
			padding: 0 50px;
			
			.myContentList{
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 32px;
				color: #fff;
				padding-bottom: 28px;
				border-bottom:1px solid #CCCCCC;
				margin-bottom: 28px;
				
				&>div:nth-child(1){
					display: flex;
					align-items: center;
					
					.van-iconfont{
						color: #4d95fc;
						font-size: 20px;
						margin-right: 10px;
					}
				}
				
				&>div:last-child{
					color: #4D95FC;
					position: relative;
					
					.van-iconfont{
						font-size: 18px;
					}
				}
			}
			
			.myContentList:last-child{
				margin-bottom: 0;
			}
		}
	}
	
	.van-hairline--bottom::after{
		border: none;
	}
	.van-popup{
		background-color: #135E99;
		color: #F1F1F1;
	}
	
	.languageSelect{
		font-size: 32px;
		display: flex;
		align-items: center;
		padding:  20px 46px;
		line-height: 1;
		border-top: 1px solid #fff;
		position: relative;
		
		&>img{
			width: 52px;
			margin-right: 20px;
		}
		
		.hook{
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 50px;
		}
	}
	
	.languageSelectTitle{
		font-size: 32px;
		text-align: center;
		padding:  20px 0;
	}
	
	.userProp{
		background-color: transparent;
	}
	.userStatusBox{
		width: 585px;
		height: 399px;
		background-color: #3880CE;
		border-radius: 10px;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 28px 0 48px;
		position: relative;
		
		&>img{
			position: absolute;
			width: 39px;
			top: 32px;
			right: 28px;
		}
		
		&>div{
			width: 500px;
			font-size: 36px;
		}
		
		&>.userStatusTitle{
			text-align: center;
			padding-bottom: 34px;
			border-bottom: 2px solid #093249;
		}
		
		&>.userStatusContent{
			padding: 34px 0;
			border-bottom: 2px solid #093249;
			display: flex;
			justify-content: space-between;
		}
	}
</style>