<template>
    <div class="box">
        <div class="header">
            <div v-lazy:background-image="require('../../../assets/can-join.png')">
                <div>
                    <!-- <self-icon class="icon" type="can-join"></self-icon> -->
                    <span>{{$t('YouMayJoin')}}</span>
                </div>
                <span>{{ kecanyu }}<br />GABA</span>
            </div>
            <div v-lazy:background-image="require('../../../assets/current-join.png')">
                <div>
                    <!-- <self-icon class="icon" type="outline"></self-icon> -->
                    <span>{{$t('CurrentEntry')}}</span>
                </div>
                <span>{{ currentJoin }}<br />GABA</span>
            </div>
            <div v-lazy:background-image="require('../../../assets/total-join.png')">
                <div>
                    <!-- <self-icon class="icon" type="total-join"></self-icon> -->
                    <span>{{$t('CumulativeEntry')}}</span>
                </div>
                <span>{{ totalJoin }}<br />GABA</span>
            </div>
        </div>
        <div class="content">
			<div class="gaba">
				{{$t('Balance')}}：{{gaba}} GABA
			</div>
            <div class="title">
                <i></i>
                <span>{{$t('FillQuota')}}</span>
            </div>
            <div class="input">
				<field class="inputInput" v-model="inputValue" placeholder="0" @input="InputValue" />
                <span>GABA</span>
            </div>
			<slider v-model="amount" :step='0.1' :max="max" @input='sliderInput' >
				<div slot="button" class="custom-button ">
				    {{ status == '4' ? 0 : ((amount / max)*100).toFixed(0) }}%
				 </div>
			</slider>
        </div>
        <div class="footer">
            <Button @click="join" :disabled="!checked">{{$t('sure')}}</Button>
            <checkbox class="checkbox" v-model="checked" @click='check'>
                {{$t('IHaveRead')}}<span style="color: #4D95FC;">《{{$t('RulesEngagement')}}》</span>，{{$t('involved')}}
            </checkbox>
        </div>
        <!-- <Overlay :show="show" @click="show = false">
            <div class="overlay" @click.stop v-lazy:background-image="require('../../../assets/limit-bg.png')">
                <img v-lazy="require('../../../assets/limit-close.png')" @click="show = false" />
            </div>
        </Overlay> -->
		
		<popup v-model="show" @click-overlay='clickOverlay'>
			<div class="RiskTipsBox">
				<span>{{$t('RulesEngagement')}}</span>
				<div class="RiskTipsText">
					<div>{{$t('RiskTipsText1')}}</div>
					<div class="em2">{{$t('RiskTipsText2')}}</div>
					<div class="em2">{{$t('RiskTipsText3')}}</div>
					<div class="em2">{{$t('RiskTipsText4')}}</div>
					<div class="em2">{{$t('RiskTipsText5')}}</div>
					<div class="em2">{{$t('RiskTipsText6')}}</div>
					<div class="em2">{{$t('RiskTipsText7')}}</div>
					<div class="em2">{{$t('RiskTipsText8')}}</div>
				</div>
				<button @click="show=false;checked=true;">{{$t('agree')}}</button>
			</div>
		</popup>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun" 
		:needAuthorization='amount' 
		:availableAuthorizations="authorizedAccessAmount" 
		:tokenType="'GABA'"
		@authorizationEnds="end"
		@authorizedAccessInput="getNumber"></authorization>
    </div>
</template>

<script>
import { Checkbox, Overlay, Button, Toast , Popup , Slider , Field } from 'vant';
import Authorization from '../../../components/Authorization.vue';

export default {
    components: { Checkbox, Overlay, Button  , Popup , Authorization , Slider , Field },
	data() {
        return {
            currentJoin: 0,
            totalJoin: 0,
            amount: 0,
            allCanJoin: 0,
            show: false,
            checked: false,
			max: 100,
			inputValue : '',
			gaba : 0,
			status: '',
			kecanyu: 0,
			shengyu: 0,
			authorizedAccess: false,
			authorizedAccessAmount: 0,
        }
    },
    methods: {
        join() {
			let amount = Number(this.amount)
			
			var text = this.amount.toString().split('.');
			if(text[1] && text[1].length > 3 && this.shengyu >= 0.001){
				return Toast.fail(this.$t('Must'))
			}else if(amount <= 0){
				return Toast.fail(this.$t('Must0'))
			}else if(amount > this.gaba){
				return Toast.fail(this.$t('InsufficientBalance'))
			}else  if(amount > this.authorizedAccessAmount){
				return this.authorizedAccess = true;
			}

            this.$contract.mainContract.methods.DoDeposit(this.$toWei(this.amount.toString())).send({
                from: this.$address,
                // value: this.$toWei(this.amount)
            }).on('transactionHash' , txHash =>{
                console.log(txHash)
            }).on('error' , console.error)
            .on('receipt' , result=>{
                console.log(result)
                this.amount = 0
                this.getData()
				this.$router.go(-1);
            })
        },
        init() {
            this.getData()
        },
        async getData() {
			this.authorizedAccess = false;
			var authorizedAccess = await this.$contract.mainContract.methods.CurrentAllowance().call({from:this.$address})
			this.authorizedAccessAmount = this.$fromWei(authorizedAccess)
			
			if(localStorage.getItem('check')){
				this.checked = JSON.parse(localStorage.getItem('check'))
			}else{
				this.checked = false
			}
            this.$contract.mainContract.methods.RoundStatusAt(this.$route.query.oid).call(async (err,result)=>{
				this.status = result.status;
				if(result.status == '4'){return}
				this.shengyu = this.$fromWei(result.totalAmount) - this.$fromWei(result.currentAmount)
				var list = await this.$contract.mainContract.methods.DepositedInfo(this.$address,result.oid).call();
				this.currentJoin = this.$fromWei(list.totalAmount)
				var depositMaxLimitProp = await this.$contract.mainContract.methods._depositMaxLimitProp().call();
				depositMaxLimitProp /= 1000;
				this.max = (this.$fromWei(result.totalAmount) * depositMaxLimitProp - this.currentJoin).toFixed(3);
				this.max = Number(this.max.replace(/(0+)$/g,""))
				this.kecanyu = this.max
				// this.max = this.$fromWei(result.totalAmount,2) - this.$fromWei(result.currentAmount,2);
			})
			
			// 获取token余额
			this.$contract.gabaToken.methods.balanceOf(this.$address).call((err, res) => {
			    if (!err) {
			        this.gaba = this.$fromWei(res)
			    }
			})
			
			// 获取累计参与
			this.$contract.mainContract.methods.CurrentDepositedTotalCount(this.$address).call((err, res) => {
			    if (!err) {
			        this.totalJoin = this.$fromWei(res)
			    }
			})
        },
		check(){
			this.show = true;
			localStorage.setItem('check','true')
		},
		clickOverlay(){
			this.show = false;
			this.checked = false;
			localStorage.setItem('check','false')
		},
		InputValue(){
			this.inputValue = this.inputValue.replace(/[^0-9.]/g,'')
			
			var value = Number(this.inputValue);
			if(value > this.max){
				this.amount = this.max
				this.inputValue = this.max
			}else{
				this.amount = Number(this.inputValue)
			}
		},
		sliderInput(){
			var amout = Number(this.amount);
			this.inputValue = amout
		},
		authorizedAccessFun(){
			// console.log(this.$contract.mainContractAddress)
			this.$contract.gabaToken.methods.approve(this.$contract.mainContractAddress,this.$web3.utils.toWei(this.authorizedAccessNumber.toString(),'ether')).send({
				from : this.$address
			}).on('transactionHash' , txHash =>{
				console.log(txHash)
			}).on('error' , err=>{
				console.log(err)
			}).on('receipt' , result=>{
				this.init()
			})
		},
		getNumber(value){
			this.authorizedAccessNumber = value;
		},
		end(){
			this.authorizedAccess = false;
		}
    },
    mounted() {
        this.init()
    }
}
</script>

<style lang="scss">
.checkbox {
    span {
        color: #fff;
        font-size: 24px;
    }
}
.van-slider{
	width: 95%;
	margin: 0 auto;
}
.van-slider, .van-slider__bar{
	background-color: #DEE1E2;
}
.van-slider__bar{
	height: 15px!important;
	background: url(../../../assets/sliderBg.png) , linear-gradient(to right, #0081EA, #7FFFE0);
}
.custom-button{
	width: 60px!important;
	height: 60px!important;
	background: linear-gradient(#5AB7FB, #0C2FA2);
	border-radius: 50%;
	font-size: 12px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.van-field__control{
	color: #fff;
}
</style>

<style lang="scss" scoped>
.van-popup{
	background:rgba(56,128,206,1);
	opacity:0.98;
	border-radius: 10px;
}
.RiskTipsBox{
	width:710px;
	height:968px;
	padding: 18px 54px 48px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	
	span{
		font-size: 40px;
	}
	
	.RiskTipsText{
		width: 100%;
		height: 730px;
		margin: 20px 0;
		font-size: 30px;
		overflow-y: scroll;
		text-align: left;
		
		.em2{
			text-indent: 2em;
		}
		
	}
	
	button{
		width: 340px;
		height: 68px;
		background-color: #0F35A6;
		border: none;
		font-size: 32px;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 7px;
	}
}
.box{
	width: 100vw;
	height: 100vh;
}
.overlay {
    width: 522px;
    height: 580px;
    margin: 166px auto 0;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    &>img {
        position: absolute;
        width: 60px;
        height: 60px;
        right: 42px;
    }
}
.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    &>button {
        border: none;
        outline: none;
        background-color: #0083F1;
        width: 340px;
        height: 72px;
        font-size: 32px;
        color: #fff;
        border-radius: 8px;
        margin-bottom: 40px;
    }
}
.content {
    padding: 0 40px;
	
	&>.gaba{
		font-size: 24px;
		display: flex;
		margin-bottom: 14px;
	}
  
    &>.amount {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 0 40px;
        &>div {
            display: flex;
            align-items: center;
            font-size: 24px;
            color: #FCFCFC;
            &>img {
                width: 40px;
                height: 40px;
                margin-right: 12px;
            }
        }
    }
    &>.input {
        width: 100%;
        padding: 20px 0 16px;
		color: #fff;
        font-size: 48px;
        font-weight: bold;
        display: flex;
        align-items: center;
        border-bottom: 2px solid #DEDCDC;
		margin-bottom: 60px;
        &>.inputInput {
            font-size: 48px;
            font-weight: bold;
            width: 100%;
            border: none;
            outline: none;
            background-color: transparent;
			padding: 0;
        }
		.van-cell:not(:last-child)::after{
			display: none;
		}
		input{
			color: #fff!important;
		}
    }
    &>.title {
        display: flex;
        align-items: center;
        &>i {
            width: 8px;
            height: 32px;
            background-color: #4D95FC;
            margin-right: 12px;
        }
        &>span {
            font-size: 32px;
            color: #fff;
            font-weight: bold;
        }
    }
}
.header {
    padding: 40px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &>div {
        width: 220px;
        height: 145px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        padding: 20px 20px 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        color: #fff;
        font-size: 32px;
		border-radius: 15px;
		
        &>div {
            display: flex;
            align-items: center;
            font-size: 28px;
            &>.icon {
                font-size: 50px;
                margin-right: 20px;
            }
        }
    }
}
</style>